import React from "react";
import {
  Cell,
  Label,
  Pie,
  PieChart,
  ResponsiveContainer,
  Tooltip,
} from "recharts";
import styled from "styled-components";
import PropTypes from "prop-types";
import { PieTooltip } from "./CustomTooltip";
                   
const customLabel = ({ cx, cy, midAngle, outerRadius, fill, payload, percent, value }) => {
  const RADIAN = Math.PI / 180;
  const sin = Math.sin(-RADIAN * midAngle);
  const cos = Math.cos(-RADIAN * midAngle);
  const sx = cx + (outerRadius + 6) * cos;
  const sy = cy + (outerRadius + 6) * sin;
  const mx = cx + (outerRadius + 20) * cos;
  const my = cy + (outerRadius + 20) * sin;
  const ex = mx + (cos >= 0 ? 1 : -1) * 14;
  const ey = my;
  const textAnchor = cos >= 0 ? 'start' : 'end';

  return (
    <g>
      <path d={`M${sx},${sy}L${mx},${my}L${ex},${ey}`} stroke={fill} fill="none"/>
      <circle cx={ex} cy={ey} r={2} fill={fill} stroke="none"/>
      <text x={ex + (cos >= 0 ? 1 : -1) * 12} y={ey} textAnchor={textAnchor} fill="#333">{payload.shortcut}</text>
      <text x={ex + (cos >= 0 ? 1 : -1) * 12} y={ey} dy={18} textAnchor={textAnchor} fill="#999">
        {`(${(percent * 100).toFixed(1)}%)`}
      </text>
    </g>
  );
};

const CustomInner = ({viewBox, innerContent, innerRadius}) => {
  const percentage = parseInt(innerRadius, 10);
  const R = Math.min(viewBox.cx, viewBox.cy) * percentage / 100;
  return (
    <g transform={`translate(${viewBox.cx - R}, ${viewBox.cy - R})`}>
      <foreignObject width={2*R} height={2*R}>
        <FullDiv>
          <InnerDiv>
            {innerContent}
          </InnerDiv>
        </FullDiv>
      </foreignObject>
    </g>
  );
}
  

const FullDiv = styled.div`
position: relative;
height: 100%;
width: 100%;
border-radius: 50%;
`;

const InnerDiv = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
`;

const getColor = ({color, value}, idx, colorMapper) =>
  colorMapper
  ? colorMapper({value, color, idx})
  : color;

const prepareData = ({data, descriptionMapper, shortcutMapper, showEmpty, ...rest}) =>
  data.filter(({value}) => !showEmpty || value).map(
    e => ({
      ...e,
      description: descriptionMapper ? descriptionMapper(e.key) : e.key,
      shortcut: shortcutMapper ? shortcutMapper(e.key) : e.key,
      ...rest,
    }));

export default Object.assign(
({
  data,
  unit,
  showEmpty,
  descriptionMapper,
  shortcutMapper,
  showTooltip,
  colorMapper,
  innerContent,
  innerRadius = "32%",
  outerRadius = "48%",
  dataFormatter,
  ...props
}) => (
<StyledPieChart>
  <ResponsiveContainer height="100%" width="100%">
    <PieChart>
      {showTooltip &&
        <Tooltip
          content={
            <PieTooltip dataFormatter={dataFormatter}/>
          }
      />}
      <Pie
          startAngle={-270}
          endAngle={90}
          data={prepareData({
            data,
            descriptionMapper,
            shortcutMapper,
            showEmpty,
            unit,
          })}
          {...(innerContent && {innerRadius})}
          outerRadius={outerRadius} 
          label={customLabel}
          dataKey="value"
          minAngle={2}
          {...props}
      >
          {
            data.map(({key, value, color}, idx) => <Cell key={`cell-${key}`} fill={getColor({color, value}, idx, colorMapper)}/>)
          }
          {
            innerContent &&
              <Label content={<CustomInner innerContent={innerContent} innerRadius={innerRadius}/>} />
          }
      </Pie>
    </PieChart>
  </ResponsiveContainer>
</StyledPieChart>),
{
    propTypes: {
        data: PropTypes.arrayOf(PropTypes.shape({
            key: PropTypes.string.isRequired,
            value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
            color: PropTypes.string,
        })),
    },
},
)

const StyledPieChart = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  .recharts-layer {
    font-size: 1.1rem;
    @media (max-width: 1600px) {
        font-size: 1em;
    }
    @media (max-width: 1440px) {
        font-size: 0.9em;
    }
  }
`;
