import React, { useState, useRef, useContext } from 'react';
import styled from 'styled-components';
import { StickyContainer, Sticky } from 'react-sticky';
import { Empty } from 'antd';

import { AppContext } from '../../../AppDataProvider';
import { getReportData } from '../services/api';

import ReportConfig from './ReportConfig';
import ConfirmLoad from './ConfirmLoad';
import ReportGeneration from './ReportGeneration';
import Section from '../components/Section';
import StickySection from '../components/StickySection';
import ReportDownload from './ReportDownload';

// Istnieje due obcienie przy renderowaniu raportw
// Moliwe jest odczytanie wartoci komentarzy za pomoc DOM np:
// buildings.reduce((prev, e) => [...prev, document.getElementById(`comment-${e}-dailyHeatConsumption`)], []).map(e => e.getElementsByTagName('textarea')[0].value)
// Komentarze s zatem bindowane tylko w jedn stron (przy wgraniu)

export default () => {
    const [reportConfig, setReportConfig] = useState();
    const [reportData, setReportData] = useState();
    const [reportComments, setReportComments] = useState();
    const [reloadAlert, setReloadAlert] = useState();

    const { startAsyncTask, finishAsyncTask } = useContext(AppContext);

    const printRef = useRef();

    const loadData = async () => {
        if(reportData) {
            setReloadAlert(true);
        }
        else {
            _loadData(reportConfig);
        }
    }

    const _loadData = async (config) => {
        startAsyncTask();
        const resp = await getReportData(config);
        setReportData(resp.data);
        setReportConfig(resp.data.config);
        setReportComments(resp.data.comments);
        setReloadAlert(false);
        finishAsyncTask();
    }

    return (
        <StyledGeneration id="Generation">
            <Section
                title="Pobranie danych"
                id="LoadReportData"
            >
                <ReportConfig config={reportConfig} loadData={loadData} onChange={v => setReportConfig(v)}/>
            </Section>
            <Sticky relative>
                {({style, isSticky}) =>
                    (<div style={{
                        ...style,
                        ...(isSticky && {
                            zIndex: 1,
                            top: 56,
                        }),
                    }}>
                        <StickySection isSticky={isSticky}/>
                    </div>)}
            </Sticky>
            <Section
                id="ReportGeneration"
                style={{
                    flex: 1,
                }}
            >
                {reportData
                ? (
                <ReportGeneration
                    comments={reportComments}
                    {...reportData}
                    printRef={printRef}
                />)
                : (<Empty
                    description="Brak pobranych danych do raportu"
                />)}
            </Section>
            {reportData &&
            (<Section
                title="Pobranie raportu"
                id="ReportDownload"
            >
                <ReportDownload
                    config={reportConfig}
                    loadData={loadData}
                    onChange={v => setReportConfig(v)}
                    printRef={printRef.current}
                />
            </Section>)}
            <ConfirmLoad
                visible={reloadAlert}
                onCancel={() => setReloadAlert(false)}
                onOk={() => _loadData(reportConfig)}
            />
        </StyledGeneration>
    );
}

const StyledGeneration = styled(StickyContainer)`
    position: relative;
    height: 100%;
    width: 100%;
    overflow: auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
    @media print {
        color: black;
    }
`;
