import React, { useContext, useEffect } from 'react';
import { Tabs } from 'antd';

import { AlarmsContext } from '../AlarmsDataProvider';
import AlarmsTable from '../components/AlarmsTable';
import AlarmsSettings from './AlarmsSettings';

const AdminAlarms = () => {
  const {
    allUsersActiveAlarms,
    allUsersAlarmsHistory,
    allUsersAlarmsSettings,
    fetchAllUsersActiveAlarms,
    fetchAllUsersAlarmsHistory,
    fetchAllUsersAlarmsSettings,
    toggleIsSettingWatched,
    changeSettingTreshold,
  } = useContext(AlarmsContext);

  useEffect(() => {
    fetchAllUsersActiveAlarms();
    fetchAllUsersAlarmsHistory();
    fetchAllUsersAlarmsSettings();
  }, []);

  return (
    <div>
      <Tabs>
        <Tabs.TabPane tab="Aktywne alarmy uytkownikw" key="usersAlarms">
          <AlarmsTable alarms={allUsersActiveAlarms} isAdmin />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Historia alarmw uytkownikw" key="usersHistory">
          <AlarmsTable alarms={allUsersAlarmsHistory} isAdmin isHistory />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Ustawienia alarmw" key="settings">
          <AlarmsSettings
            settings={allUsersAlarmsSettings}
            changeTreshol={changeSettingTreshold}
            toggleIsWatched={toggleIsSettingWatched}
            allUsers={['Adam', 'Micha', 'Mateusz']}
            allBuildings={['PSP 17 Radom', 'PSP 34 Lublin']}
          />
        </Tabs.TabPane>
      </Tabs>
    </div>
  );
};

export default AdminAlarms;
