import React, { useContext } from 'react';

import CrudList from '../../../../components/CRUDs/CrudList';
import { DbContext } from '../../DbDataProvider';

const InvoicesList = () => {
  const {
    invoices,
    setCurrentId,
    setMode,
    setKind,
    setIsModalVisible,
  } = useContext(DbContext);

  const showInvoicesModal = id => {
    setIsModalVisible(true);
    setCurrentId(id);
    setKind('Invoices');
  };

  const handleCreateInvoice = () => {
    showInvoicesModal();
    setMode('create');
  };

  const handleReadInvoice = id => {
    showInvoicesModal(id);
    setMode('read');
  };

  const handleUpdateInvoice = id => {
    showInvoicesModal(id);
    setMode('update');
  };

  const handleDeleteInvoice = id => {
    showInvoicesModal(id);
    setMode('delete');
  };

  return (
    <CrudList
      items={invoices.map(invoice => ({
        name: `Faktura ${new Date(invoice.month).getMonth() + 1}-${new Date(
          invoice.month,
        ).getFullYear()}`,
        description: `Zuycie ${invoice.monthlyUsage}`,
        id: invoice.id,
      }))}
      addText="Dodaj faktur"
      onCreate={handleCreateInvoice}
      onRead={handleReadInvoice}
      onUpdate={handleUpdateInvoice}
      onDelete={handleDeleteInvoice}
    />
  );
};

export default InvoicesList;
