import React, { useState } from 'react';
import { Select, List, Switch, Input } from 'antd';

const { Option } = Select;

const AlarmsSettings = ({
  settings,
  allUsers,
  allBuildings,
  changeTreshold,
  toggleIsWatched,
}) => {
  const [selectedUser, setSelectedUser] = useState(null);
  const [selectedBuilding, setSelectedBuilding] = useState(null);

  const handleUserChange = value => setSelectedUser(value);
  const handleBuildingChange = value => setSelectedBuilding(value);

  const userOptions = allUsers.map(user => (
    <Option value={user} key={user}>
      {user}
    </Option>
  ));
  const buildingOptions = allBuildings.map(building => (
    <Option value={building} key={building}>
      {building}
    </Option>
  ));

  return (
    <div>
      <div>
        <Select
          placeholder="Wybierz uytkownika"
          style={{ width: 350, margin: 20 }}
          onChange={handleUserChange}
        >
          {userOptions}
        </Select>
        {selectedUser && (
          <Select
            placeholder="Wybierz budynek"
            style={{ width: 350 }}
            onChange={handleBuildingChange}
          >
            {buildingOptions}
          </Select>
        )}
      </div>
      <div>
        {selectedBuilding && (
          <SettingsList
            settings={settings}
            changeTreshold={changeTreshold}
            toggleIsWatched={toggleIsWatched}
          />
        )}
      </div>
    </div>
  );
};

const SettingsList = ({ settings, changeTreshold, toggleIsWatched }) => {
  return (
    <List
      style={{ margin: 20 }}
      itemLayout="horizontal"
      dataSource={settings}
      renderItem={setting => (
        <List.Item
          key={setting.name}
          actions={[
            <div style={{ display: 'flex' }}>
              <Input
                value={setting.treshold}
                onChange={value => changeTreshold(value, setting.name)}
              />
              <div style={{ marginLeft: 10 }}>{setting.unit}</div>
            </div>,
            <Switch
              checked={setting.isWatched}
              onChange={value => toggleIsWatched(value, setting.name)}
            />,
          ]}
        >
          <List.Item.Meta title={setting.name} />
        </List.Item>
      )}
    />
  );
};

export default AlarmsSettings;
