import React, { useState, useEffect } from 'react';
import axios from 'axios';
import to from 'await-to-js';
import { notification } from 'antd';
import { postSignIn, getEnums } from './scenes/SignIn/services/api';
import { getBuildings } from './scenes/Administration/services/api';

const fakeBuildings = [
  {
    id: 1,
    name: 'SP nr 17',
    measurements: ['temperature', 'co2', 'humidity', 'electricity', 'hotWater'],
  },
  {
    id: 2,
    name: 'Muzeum Kultury',
    measurements: ['temperature', 'co2', 'humidity'],
  },
  {
    id: 44,
    name: 'Dom seniora',
    measurements: ['electricity', 'hotWater'],
  },
];

const fakeCommunities = [
  { id: 1, name: 'Gmina miw', buildings: [1, 2] },
  { id: 2, name: 'Gmina Porb', buildings: [44] },
];

const AppContext = React.createContext({});

const AppDataProvider = ({ appBaseData, children }) => {
  const [isLoading, setIsLoading] = useState(false);
  const startAsyncTask = () => setIsLoading(true);
  const finishAsyncTask = () => setIsLoading(false);
  const [isSignedIn, setIsSignedIn] = useState(
    Boolean(localStorage.getItem('user-token')),
  );
  const [buildings, setBuildings] = useState(fakeBuildings);
  const [communities, setCommunities] = useState(fakeCommunities);
  const [email, setEmail] = useState();
  const [userId, setUserId] = useState();
  const [anyActiveAlarms, setAnyActiveAlarms] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const [appHeader, setAppHeader] = useState();
  const [weatherStationName, setWeatherStationName] = useState();
  const [enums, setEnums] = useState();

  const submitLogin = async (email, password) => {
    startAsyncTask();
    const [err, resp] = await to(postSignIn(email, password));
    if (err) {
      notification.error({ message: 'Nie udao si zalogowa' });
      finishAsyncTask();
      return;
    }
    const { data, status } = resp;
    if (status === 200) {
      const token = data.token;
      localStorage.setItem('user-token', token);
      axios.defaults.headers.common.Authorization = `Bearer ${token}`;
      getBaseData();
      notification.success({ message: 'Pomylnie zalogowano' });
      setEmail(data.reportEmail);
      setUserId(data.login);
      setIsAdmin(data.roleId === 1);
      setAnyActiveAlarms(data.anyActiveAlarms || true);
      setAppHeader(data.appHeader);
      setWeatherStationName(data.weatherStationName);
    }
    finishAsyncTask();
  };

  const getBaseData = async () => {
    const [err, buildingsResp] = await to(getBuildings());
    if (err) console.err('Get buildings eroor: ', err);
    const [enumErr, enumsResp] = await to(getEnums());
    if (enumErr) console.err('Get enums eroor: ', enumErr);

    // FIX ME MATEUSZ MICHALEWSKI :)
    buildingsResp.data = [
      {
        id: 1,
        name: 'SP nr 17',
        measurements: [
          'temperature',
          'co2',
          'humidity',
          'electricity',
          'hotWater',
        ],
      },
      {
        id: 2,
        name: 'Muzeum Kultury',
        measurements: ['temperature', 'co2', 'humidity'],
      },
      {
        id: 44,
        name: 'Dom seniora',
        measurements: ['electricity', 'hotWater'],
      },
    ];
    setBuildings(buildingsResp.data);
    setEnums(enumsResp.data);
    setIsSignedIn(true);
  };

  useEffect(() => {
    if (appBaseData) {
      const { buildings, enums, appHeader } = appBaseData;
      console.log(appBaseData, 'xd');
      setBuildings(buildings);
      setEnums(enums);
      setAppHeader(appHeader);
      setIsAdmin(true);
    }
  }, [appBaseData]);

  const state = {
    isSignedIn,
    isAdmin,
    isLoading,
    email,
    userId,
    anyActiveAlarms,
    weatherStationName,
    appHeader,
    buildings,
    communities,
    enums,
    startAsyncTask,
    finishAsyncTask,
    setIsSignedIn,
    setEmail,
    setUserId,
    setBuildings,
    setCommunities,
    setAnyActiveAlarms,
    setIsAdmin,
    submitLogin,
  };

  return <AppContext.Provider value={state}>{children}</AppContext.Provider>;
};

export { AppContext };
export default AppDataProvider;
