import React from 'react';
import styled from 'styled-components';
import { Input } from 'antd';

export default ({children, ...props}) =>
    <DisabledInput
        {...props}
        disabled
    >
        {children}
    </DisabledInput>

const DisabledInput = styled(Input)`
    &.ant-input[disabled] {
        color: rgba(0, 0, 0, 0.65);
        cursor: text;
    }
`;
