import React from 'react';
import styled from 'styled-components';
import ChartHolder, { ChartHolderItem } from '../components/ChartHolder';
import reportConfig from './reportConfig';
import CustomChart from '../../../components/CustomChart';
import AreaExample from './AreaExample';
import ScatteredExample from './ScatteredExample';

const WrappedChart = ({chartSize, ...props}) =>
    <ChartHolderItem size={chartSize}>
        <CustomChart {...props} />
    </ChartHolderItem>

export default ({loadedData}) => {

    const passedChartConfig = (key) =>
    ({
        data: loadedData[key].data,
        ...reportConfig[key](loadedData[key].data),
    });

    return (<StyledReport>
        <div>
            {
                Array(12).fill(1).map((e, idx) =>
                    <TestElement width={idx*100} key={idx} />,
                )
            }
            <AreaExample />
            <ChartHolder>
                <WrappedChart {...passedChartConfig("dailyHeatConsumption")}/>
                <WrappedChart {...passedChartConfig("dailyElectricityConsumption")}/>
            </ChartHolder>
            <ChartHolder>
                <WrappedChart {...passedChartConfig("comparedConsumption")}/>
                <WrappedChart {...passedChartConfig("heatingCurve")}/>
                <WrappedChart {...passedChartConfig("energyUsageAreas")}/>
            </ChartHolder>
            <ChartHolder>
                <WrappedChart {...passedChartConfig("dailyOZEProduction")}/>
            </ChartHolder>
            <ScatteredExample length={20} />
        </div>
    </StyledReport>);
}

const TestElement = ({width}) =>
    <div style={{
        width,
        height: '20px',
        background: 'red',
        margin: '10px 0',
    }}>{width}</div>

const StyledReport = styled.div`
    width: 880px;
`;
