import React, { useState, useContext } from 'react';
import { Button, DatePicker } from 'antd';

import NumberField from '../../../../components/CRUDs/NumberField';
import SelectField from '../../../../components/CRUDs/SelectField';
import { DbContext } from '../../DbDataProvider';

const { MonthPicker } = DatePicker;

const InvoiceForm = ({
  editable,
  initialState,
  actionLabel,
  onCancel,
  onAction,
  removeMode = false,
}) => {
  const data = initialState || {
    month: null,
    monthlyUsage: 0,
    hookUpId: undefined,
  };

  const [formData, setFormData] = useState(data);
  const { hookUps } = useContext(DbContext);

  const onChange = (fieldName, value) => {
    setFormData({
      ...formData,
      [fieldName]: value,
    });
  };

  const handleAction = () => {
    onAction({
      ...formData,
      month: formData.month.format(),
    });
    onCancel();
  };

  return (
    <div>
      {removeMode ? (
        <div>Czy na pewno chcesz usun faktur ? </div>
      ) : (
        <>
          <MonthPicker
            style={{ margin: 15 }}
            format="MM-YYYY"
            value={formData.month}
            onChange={v => onChange('month', v)}
            placeholder="Wybierz miesic"
          />
          <NumberField
            editable={editable}
            onChange={onChange}
            formData={formData}
            name="monthlyUsage"
            label="Zuycie"
          />
          <SelectField
            placeholder="Wybierz przycze"
            onChange={v => onChange('hookUpId', v)}
            disabled={!editable}
            value={formData.hookUpId}
            items={hookUps}
          />
        </>
      )}
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          marginTop: '20px',
          padding: '0 30px',
        }}
      >
        <Button onClick={onCancel}>Anuluj</Button>
        <Button type="primary" onClick={handleAction}>
          {actionLabel}
        </Button>
      </div>
    </div>
  );
};

export default InvoiceForm;
