import React, { useState } from 'react';
import styled from 'styled-components';
import { Button } from 'antd';

import ChartSelect from '../components/ChartSelect';

export default ({onSelect}) => {
    const [type, setType] = useState(null);
    const [error, setError] = useState(false);

    const apply = () => {
        if(type === null) return setError(true);
        return onSelect(type);
    }

    const setChartType = (key) => {
        if(error) setError(false);
        setType(key);
    }

    return (<ChartTypeSelect>
        <TypeSelection>
            <ChartSelect type={type} setType={setChartType} error={error}/>
        </TypeSelection>
        <Next onClick={apply}>
            <StyledNextButton type="primary" size="large">
                Dalej
            </StyledNextButton>
        </Next>
  </ChartTypeSelect>);
}

const ChartTypeSelect = styled.div`
    position: relative;
    height: 100%;
`;

const TypeSelection = styled.div`
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
`;

const Next = styled.div`
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 20px;
`;

const StyledNextButton = styled(Button)`
    min-width: 160px;
`;
