import React from 'react';
import styled from 'styled-components';
import { Button, Icon } from 'antd';

import Section from './Section';


export default ({isSticky, ...props}) => (
    <StyledStickySection isSticky={isSticky} {...props}>
        <Section.Title>Generowany raport</Section.Title>
        <ButtonContainer>
            <Button.Group>
                {isSticky && (<Button href="#LoadReportData">
                    <Icon type="up" />
                    Do gry
                </Button>)}
                <Button href="#ReportDownload">
                    Do dou
                    <Icon type="down" />
                </Button>
            </Button.Group>
        </ButtonContainer>
    </StyledStickySection>
);

const StyledStickySection = styled.div`
    position: relative;
    padding: 8px 20px 0;
    margin: 0 -20px;
    ${({isSticky}) => isSticky ? `
        border-bottom: solid 1px #EEE;
        background: white;
    ` : ''}
`;

const ButtonContainer = styled.div`
    position: absolute;
    right: 20px;
    top: 8px;
`;
