import React, { Fragment } from 'react';
import styled from 'styled-components';

import { Empty } from 'antd';
import { getDisableReason, NOT_LOADED_DATA_REASONS } from './ChartSetup/utils/constants';

export default ({loadedData, usedData, config}) => {
    const status = usedData.map(({show, dataSource}) => {
        const disableReason = getDisableReason(
            dataSource,
            loadedData,
            config.xAxis.xKey,
        );
        return {
            show,
            noAxisData: disableReason === NOT_LOADED_DATA_REASONS.NOAXISDATA,
            asKey: disableReason === NOT_LOADED_DATA_REASONS.AXISKEY,
            canShow: disableReason === null,
        };
    });
    const canShowData = status.find(e => e.canShow);
    const onlyXkey = status.reduce((prev, e) => prev && e.asKey, true);
    let description = null;
    if( usedData.length === 0) {
        description = (<StyledDescription>
            <Tip>W tym miejscu pojawi si wykres.</Tip>
            <Tip>Pobierz dane do wywietlenia w sekcji <b>Pobranie danych</b></Tip>
        </StyledDescription>);
    }
    else if(canShowData) {
        description = (<StyledDescription>
            <Tip>Aby pokaza dane, zmie ich wywietlanie za pomoc suwaka.</Tip>
            <Tip>Konfiguracja wywietlania ma miejsce w sekcji <b>Zaadowane dane</b></Tip>
        </StyledDescription>);
    }
    else if(onlyXkey) {
        description = (<StyledDescription>
            <Tip>Pobrane zostay tylko dane odkadane na osi X.</Tip>
            <Tip>Aby pobra dane do wywietlenia na innych osiach, pobierz inne dany w sekcji <b>Pobranie danych </b>
            lub zmie dane z osi odniesienia na zakres czasowy w sekcji <b>Konfiguracja wykresu - O pozioma (odniesienia)</b>.</Tip>
        </StyledDescription>);
    }
    else {
        description = (<StyledDescription>
            <Tip>Pobrane dane nie maj odpowiadajcych im danych na osi X.</Tip>
            <Tip>Wybr danych odkadanych na osi X mona zmieni w sekcji <b>Konfiguracja wykresu - O pozioma (odniesienia)</b>.</Tip>
        </StyledDescription>);
    }

    return (<NoChartReason
        image="https://gw.alipayobjects.com/mdn/miniapp_social/afts/img/A*pevERLJC9v0AAAAAAAAAAABjAQAAAQ/original"
        description={description}
    />);
}

const NoChartReason = styled(Empty)`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
`;

const StyledDescription = ({children}) =>
    <Fragment>
        {React.Children.map(children, (child, id) => (
            <Fragment>
                {child}
                {id + 1 < React.Children.count(children) && <br />}
            </Fragment>
        ))}
    </Fragment>

const Tip = styled.span`
    font-size: 1rem;
    & + & {
        margin-top: 4px;
    }
`;
