import React, { useState, useContext } from 'react';

import { AppContext } from '../../AppDataProvider';
import {
  getBuildings,
  postNewBuilding,
  postBuildingUpdate,
  postBuildingRemoval,
  getUsers,
  postNewUser,
  postUserUpdate,
  postUserRemoval,
} from './services/api';

const AdministrationContext = React.createContext({});

const AdministrationDataProvider = ({ children }) => {
  const { startAsyncTask, finishAsyncTask } = useContext(AppContext);

  const [buildings, setBuildings] = useState([]);
  const [users, setUsers] = useState([]);

  const [currentId, setCurrentId] = useState(null);
  const [mode, setMode] = useState(null);
  const [kind, setKind] = useState(null);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const getCurrentEntry = () => {
    const data = kind === 'Users' ? users : buildings;
    const currentEntry = data.find(entry => entry.id === currentId);
    return currentEntry;
  };
  const getIndexById = id => {
    const data = kind === 'Users' ? users : buildings;
    const index = data.findIndex(entry => entry.id === id);
    return index;
  };

  async function fetchUsers() {
    startAsyncTask();
    const { data } = await getUsers();
    finishAsyncTask();
    setUsers(data);
  }
  function createUser(newUser) {
    postNewUser({ ...newUser });
    setUsers([...users, newUser]);
  }
  function updateUser(updatedUser) {
    postUserUpdate(updatedUser);
    const newUsers = [...users];
    const updateIndex = getIndexById(updatedUser.id);
    newUsers[updateIndex] = updatedUser;
    setUsers(newUsers);
  }
  function deleteUser(userToDelete) {
    postUserRemoval(userToDelete.id);
    const newUsers = [...users];
    const deleteIndex = getIndexById(userToDelete.id);
    newUsers.splice(deleteIndex, 1);
    setUsers(newUsers);
  }

  async function fetchBuildings() {
    startAsyncTask();
    const { data } = await getBuildings();
    finishAsyncTask();
    setBuildings(data);
  }
  function createBuilding(newBuilding) {
    postNewBuilding(newBuilding);
    setBuildings([...buildings, newBuilding]);
  }
  function updateBuilding(updatedBuilding) {
    postBuildingUpdate(updatedBuilding);
    const newBuildings = [...buildings];
    const updateIndex = getIndexById(updatedBuilding.id);
    newBuildings[updateIndex] = updatedBuilding;
    setBuildings(newBuildings);
  }
  function deleteBuilding(BuildingToDelete) {
    postBuildingRemoval(BuildingToDelete.id);
    const newBuildings = [...buildings];
    const deleteIndex = getIndexById(BuildingToDelete.id);
    newBuildings.splice(deleteIndex, 1);
    setBuildings(newBuildings);
  }

  const state = {
    buildings,
    fetchBuildings,
    createBuilding,
    updateBuilding,
    deleteBuilding,

    users,
    fetchUsers,
    createUser,
    updateUser,
    deleteUser,

    mode,
    setMode,
    kind,
    setKind,
    currentConfig: `${mode}${kind}`,

    setCurrentId,
    currentEntry: getCurrentEntry(),

    isModalVisible,
    setIsModalVisible,
  };

  return (
    <div>
      <AdministrationContext.Provider value={state}>
        {children}
      </AdministrationContext.Provider>
    </div>
  );
};

export { AdministrationContext };
export default AdministrationDataProvider;
