import React from 'react';
import { Icon, Button } from 'antd';
import { withRouter } from 'react-router';

import { AlarmsRoute } from '../Routes';

const AlarmWarning = ({ history }) => {
  const navigateToAlarms = () => history.push(AlarmsRoute);

  return (
    <div
      style={{
        padding: 12,
        background: '#f5222d',
        display: 'flex',
        alignItems: 'center',
      }}
    >
      <Icon
        type="alert"
        style={{ color: 'white', fontSize: 30, marginRight: 10 }}
      />
      <Button type="danger" onClick={navigateToAlarms}>
        Id do alarmw
      </Button>
    </div>
  );
};

export default withRouter(AlarmWarning);
