import React, { useContext } from 'react';

import { AppContext } from '../../../../AppDataProvider';
import CrudList from '../../../../components/CRUDs/CrudList';
import { DbContext } from '../../DbDataProvider';

const HookUpsList = () => {
  const {
    hookUps,
    setCurrentId,
    setMode,
    setKind,
    setIsModalVisible,
  } = useContext(DbContext);

  const { enums } = useContext(AppContext);

  const showHookUpsModal = id => {
    setIsModalVisible(true);
    setCurrentId(id);
    setKind('HookUps');
  };

  const handleCreateHookUp = () => {
    showHookUpsModal();
    setMode('create');
  };

  const handleReadHookUp = id => {
    showHookUpsModal(id);
    setMode('read');
  };

  const handleUpdateHookUp = id => {
    showHookUpsModal(id);
    setMode('update');
  };

  const handleDeleteHookUp = id => {
    showHookUpsModal(id);
    setMode('delete');
  };

  return (
    <CrudList
      items={hookUps.map(hookUp => ({
        name: hookUp.name,
        description: `${enums.hookUpTypes[hookUp.type].name}, nr ${hookUp.number}`,
        id: hookUp.id,
      }))}
      addText="Dodaj przycze"
      onCreate={handleCreateHookUp}
      onRead={handleReadHookUp}
      onUpdate={handleUpdateHookUp}
      onDelete={handleDeleteHookUp}
    />
  );
};

export default HookUpsList;
