import React, { useContext, useEffect } from 'react';
import { Tabs } from 'antd';

import { DbContext } from './DbDataProvider';

import BuildingsList from './scenes/Buildings/BuildingsList';
import HookUpsList from './scenes/HookUps/HookUpsList';
import InvoicesList from './scenes/Invoices/InvoicesList';
import DataExport from './scenes/DataExport/DataExport';
import CommonModal from './scenes/CommonModal';

const Db = () => {
  const { fetchBuildings, fetchHookUps, fetchInvoices } = useContext(DbContext);

  useEffect(() => {
    fetchBuildings();
  }, []);
  useEffect(() => {
    fetchHookUps();
  }, []);
  useEffect(() => {
    fetchInvoices();
  }, []);

  return (
    <div style={{ padding: '20px' }}>
      <Tabs defaultActiveKey="buildings">
        <Tabs.TabPane tab="Budynki" key="buildings">
          <BuildingsList />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Przycza" key="hookUps">
          <HookUpsList />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Faktury" key="invoices">
          <InvoicesList />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Eksport Danych" key="dataExport">
          <DataExport
            buildings={[{ id: 1, name: 'Szkoa' }, { id: 2, name: 'Szpital' }]}
            buildingsDatas={[
              { id: 11, name: 'Powierzchnia' },
              { id: 12, name: 'Rok budowy' },
            ]}
            invoices={[{ id: 21, name: 'Prd' }, { id: 22, name: 'Woda' }]}
          />
        </Tabs.TabPane>
      </Tabs>
      <CommonModal />
    </div>
  );
};

export default Db;
