import React, { useState, useEffect } from 'react';
import ReactToPrint from 'react-to-print';
import styled from 'styled-components';
import { Button, DatePicker, Form, Input } from 'antd';

import FormItemWrapper, { validate } from '../../../components/FormItemWrapper';

import { withDelayedUpdate } from "../../../utils/delayed";

const DelayedInput = withDelayedUpdate(Input);

export default ({printRef, config = {}, onChange}) => {
    const [_date, setDate] = useState();
    const [_author, setAuthor] = useState();

    useEffect(() => {
        setAuthor(config.author);
    }, [config.author]);

    useEffect(() => {
        setDate(config.date);
    }, [config.date]);

    const onSubmit = (event) => {
        event.preventDefault();
    }

    const isFormValid = () => {
        const toValidate = [
            {key: 'author', value: _author},
            {key: 'date', value: _date},
        ];
        return toValidate.map(({key, value}) => validate({
            ...formRules[key],
            value,
            rules: formRules[key] ? (formRules[key].rules || []) : [],
          })).reduce((prev, {validateStatus}) => prev && validateStatus !== 'error', true);
    }

    const onChangeAuthor = (aut, isValid) => isValid
        ? onChange({...config, author: aut})
        : setAuthor(aut);
    
    const onChangeDate = (per, isValid) => isValid
        ? onChange({...config, date: per})
        : setDate(per);

    return (
<ReportDowloadForm>
    <Form
        layout="vertical"
        hideRequiredMark={false}
        onSubmit={onSubmit}
    >
        <TopSelectItem>
            <InputContainer>
                <FormItemWrapper
                    onChange={onChangeAuthor}
                    value={_author}
                    {...formRules.author}
                >
                    <DelayedInput delay={3000}/>
                </FormItemWrapper>
            </InputContainer>
        </TopSelectItem>
        <TopSelectItem>
            <FormItemWrapper
                onChange={onChangeDate}
                value={_date}
                {...formRules.date}
            >
                <DatePicker />
            </FormItemWrapper>
        </TopSelectItem>
        <TopSelectItem>
            <ButtonContainer>
                <ReactToPrint
                    trigger={() => (
                        <Button
                            disabled={!isFormValid()}
                            type="primary"
                        >Pobierz raport</Button>
                    )}
                    content={() => printRef}
                />
            </ButtonContainer>
        </TopSelectItem>
    </Form>
</ReportDowloadForm>);
}

const formRules = {
    author: {
        label: "Autor",
        required: true,
    },
    date: {
        label: "Data powstania raportu",
        required: true,
    },
};

const ReportDowloadForm = styled.div`
    margin-bottom: -16px;
`;

const InputContainer = styled.div`
    width: 280px;
`;

const ButtonContainer = styled.div`
    padding: 0 0 0 16px;
`;

const TopSelectItem = styled.div`
    display: inline-block;
    vertical-align: middle;
    & + & {
        margin-left: 16px;
    }
`;
