import React, { useState } from 'react';
import { Button, Select, DatePicker } from 'antd';

const { Option } = Select;
const { MonthPicker } = DatePicker;

const DataExport = ({ buildings, buildingsDatas, invoices }) => {
  const [buildingsIds, setBuildingsIds] = useState([]);
  const handleBuildingSelect = values => setBuildingsIds(values);
  const buildingOptions = buildings.map(building => (
    <Option key={building.id}>{building.name}</Option>
  ));

  const buildingDataOptions = buildingsDatas.map(data => (
    <Option key={data.id}>{data.label}</Option>
  ));

  const invoiceOptions = invoices.map(invoice => (
    <Option key={invoice.id}>{invoice.label}</Option>
  ));

  const [startDate, setStartDate] = useState();
  const [endDate, setEndDate] = useState();
  const handleStartMonthSelect = value => setStartDate(value);
  const handleEndMonthSelect = value => setEndDate(value);
  const disabledStartDate = newStartDate => {
    if (!newStartDate || !endDate) {
      return false;
    }
    return newStartDate.valueOf() > endDate.valueOf();
  };
  const disabledEndDate = newEndDate => {
    if (!newEndDate || !startDate) {
      return false;
    }
    return newEndDate.valueOf() <= startDate.valueOf();
  };

  const isFormValid = buildingsIds.length > 0 && startDate && endDate;
  console.log(invoices, buildingsDatas, 'xd');
  const handleExport = () => {
    console.log({
      buildingsIds,
      startDate: startDate.toISOString(),
      endDate: endDate.toISOString(),
    });
  };
  return (
    <div>
      <div>
        <Select
          mode="multiple"
          style={{ width: 500, margin: 15 }}
          placeholder="Wybierz budynek (budynki)"
          onChange={handleBuildingSelect}
        >
          {buildingOptions}
        </Select>
      </div>
      <div>
        <Select
          style={{ width: 500, margin: 15 }}
          mode="multiple"
          placeholder="Wybierz dane o budynakch"
        >
          {buildingDataOptions}
        </Select>
      </div>
      <div>
        <Select
          style={{ width: 500, margin: 15 }}
          mode="multiple"
          placeholder="Wybierz faktur (faktury)"
        >
          {invoiceOptions}
        </Select>
      </div>
      <div>
        <MonthPicker
          disabledDate={disabledStartDate}
          value={startDate}
          onChange={handleStartMonthSelect}
          style={{ margin: 15 }}
          format="MM-YYYY"
          placeholder="Pocztkowy miesic"
        />
        <MonthPicker
          disabledDate={disabledEndDate}
          value={endDate}
          onChange={handleEndMonthSelect}
          style={{ margin: 15 }}
          format="MM-YYYY"
          placeholder="Kocowy miesic"
        />
      </div>
      <Button type="primary" onClick={handleExport} disabled={!isFormValid}>
        Eksportuj
      </Button>
    </div>
  );
};

export default DataExport;
