import React, { useContext } from 'react';
import { Modal } from 'antd';

import { DbContext } from '../DbDataProvider';
import BuildingForm from './Buildings/BuildingForm';
import HookUpForm from './HookUps/HookUpForm';
import InvoiceForm from './Invoices/InvoiceForm';

const CommonModal = () => {
  const {
    // Buildings
    updateBuilding,
    // HookUps
    createHookUp,
    updateHookUp,
    deleteHookUp,
    // Invoices
    createInvoice,
    updateInvoice,
    deleteInvoice,
    // Rest
    currentEntry,
    setMode,
    setKind,
    currentConfig,
    setIsModalVisible,
    isModalVisible,
  } = useContext(DbContext);

  const closeModal = () => {
    setIsModalVisible(false);
    setMode(undefined);
    setKind(undefined);
  };

  const buildingModalConfigs = {
    readBuildings: {
      header: 'Szczegoy Obiektu',
      component: () => (
        <BuildingForm
          actionLabel="Ok"
          initialState={currentEntry}
          onAction={closeModal}
          onCancel={closeModal}
        />
      ),
    },
    updateBuildings: {
      header: 'Edytuj Obiekt',
      component: () => (
        <BuildingForm
          editable
          actionLabel="Zapisz"
          initialState={currentEntry}
          onAction={updateBuilding}
          onCancel={closeModal}
        />
      ),
      actionLabel: 'Zapisz',
    },
  };

  const hookUpModalConfigs = {
    createHookUps: {
      header: 'Nowe przycze',
      component: () => (
        <HookUpForm
          editable
          actionLabel="Stwrz"
          onAction={createHookUp}
          onCancel={closeModal}
        />
      ),
    },
    readHookUps: {
      header: 'Szczegoy Przycza',
      component: () => (
        <HookUpForm
          actionLabel="Ok"
          initialState={currentEntry}
          onAction={closeModal}
          onCancel={closeModal}
        />
      ),
    },
    updateHookUps: {
      header: 'Edytuj Przycze',
      component: () => (
        <HookUpForm
          editable
          actionLabel="Zapisz"
          initialState={currentEntry}
          onAction={updateHookUp}
          onCancel={closeModal}
        />
      ),
      actionLabel: 'Zapisz',
    },
    deleteHookUps: {
      header: 'Usu Przycze',
      component: () => (
        <HookUpForm
          removeMode
          actionLabel="Usu"
          initialState={currentEntry}
          onAction={deleteHookUp}
          onCancel={closeModal}
        />
      ),
    },
  };

  const invoiceModalConfigs = {
    createInvoices: {
      header: 'Nowa faktura',
      component: () => (
        <InvoiceForm
          editable
          actionLabel="Stwrz"
          onAction={createInvoice}
          onCancel={closeModal}
        />
      ),
    },
    readInvoices: {
      header: 'Szczegoy Faktury',
      component: () => (
        <InvoiceForm
          actionLabel="Ok"
          initialState={currentEntry}
          onAction={closeModal}
          onCancel={closeModal}
        />
      ),
    },
    updateInvoices: {
      header: 'Edytuj Faktur',
      component: () => (
        <InvoiceForm
          editable
          actionLabel="Zapisz"
          initialState={currentEntry}
          onAction={updateInvoice}
          onCancel={closeModal}
        />
      ),
      actionLabel: 'Zapisz',
    },
    deleteInvoices: {
      header: 'Usu Faktur',
      component: () => (
        <InvoiceForm
          removeMode
          actionLabel="Usu"
          initialState={currentEntry}
          onAction={deleteInvoice}
          onCancel={closeModal}
        />
      ),
    },
  };
  const modalConfigs = {
    ...buildingModalConfigs,
    ...hookUpModalConfigs,
    ...invoiceModalConfigs,
  };

  const currentModalConfig = modalConfigs[currentConfig];
  const ModalComponent = currentModalConfig
    ? currentModalConfig.component
    : null;

  return currentModalConfig ? (
    <Modal
      title={currentModalConfig.header}
      visible={isModalVisible}
      footer={null}
      onCancel={closeModal}
      cancelText="Anuluj"
    >
      <ModalComponent />
    </Modal>
  ) : null;
};

export default CommonModal;
