import React, { useContext } from 'react';
import styled from 'styled-components';
import { Icon, Menu } from 'antd';
import { Link } from 'react-router-dom';

import {
  AdministrationRoute,
  AlarmsRoute,
  BenchmarksRoute,
  DashboardRoute,
  DataAnalyticsRoute,
  DbRoute,
  HelpRoute,
  ReportsRoute,
} from '../Routes';

import { AppContext } from '../AppDataProvider';

const Nav = () => {
  const { isAdmin } = useContext(AppContext);

  return (
    <Container>
      <Menu mode="inline" defaultSelectedKeys={[DashboardRoute]}>
        <Menu.Item key={DashboardRoute}>
          <Link to={DashboardRoute}>
            <Icon type="dashboard" />
            Pulpit
          </Link>
        </Menu.Item>
        <Menu.Item key={AlarmsRoute}>
          <Link to={AlarmsRoute}>
            <Icon type="bell" />
            Alarmy
          </Link>
        </Menu.Item>
        <Menu.Item key={DataAnalyticsRoute}>
          <Link to={DataAnalyticsRoute}>
            <Icon type="dot-chart" />
            Analiza Danych
          </Link>
        </Menu.Item>
        <Menu.Item disabled key={BenchmarksRoute}>
          <Icon type="fund" />
          Porwnanie
        </Menu.Item>
        <Menu.Item key={ReportsRoute}>
          <Link to={ReportsRoute}>
            <Icon type="file-pdf" />
            Raporty
          </Link>
        </Menu.Item>
        <Menu.Item key={DbRoute}>
          <Link to={DbRoute}>
            <Icon type="database" />
            Baza Danych
          </Link>
        </Menu.Item>
        {isAdmin && (
          <Menu.Item key={AdministrationRoute}>
            <Link to={AdministrationRoute}>
              <Icon type="setting" />
              Administacja
            </Link>
          </Menu.Item>
        )}
        <Menu.Item key={HelpRoute}>
          <Icon type="question-circle" />
          Pomoc
        </Menu.Item>
      </Menu>
    </Container>
  );
};

const Container = styled.nav`
  display: flex;
  flex-direction: column;
  margin-left: -8px;
  flex: 0 0 168px;
  max-width: 168px;
`;

export default Nav;
