import React, { useState } from 'react';
import { InputNumber, Tabs, Button } from 'antd';

import { formConfig } from './formConfig';

const IndicatorsForm = ({ indicatorsData = [] }) => {
  const [isEditing, setIsEditing] = useState(false);

  const btnLabel = isEditing ? 'Zapisz' : 'Edytuj';
  return (
    <div>
      <Tabs defaultActiveKey={formConfig[0].categoryName}>
        {formConfig.map(category => (
          <Tabs.TabPane tab={category.categoryName} key={category.categoryName}>
            {category.indicators.map(
              ({ label, unit, field, color, minmax }) => (
                <Indicator
                  isEditing={isEditing}
                  label={label}
                  value={indicatorsData[field]}
                  minmax={minmax}
                  color={color}
                  unit={unit}
                />
              ),
            )}
          </Tabs.TabPane>
        ))}
      </Tabs>
      <Button
        type="primary"
        style={{ margin: 'auto', marginTop: 30 }}
        onClick={() => setIsEditing(prev => !prev)}
      >
        {btnLabel}
      </Button>
    </div>
  );
};

const Indicator = ({ label, unit, color, minmax, value, isEditing }) => {
  const inputs = minmax ? (
    <>
      <InputNumber
        disabled={!isEditing}
        style={{ margin: 10 }}
        value={value ? value.min : ''}
      />
      <span>min</span>
      <InputNumber
        disabled={!isEditing}
        style={{ margin: 10 }}
        value={value ? value.max : ''}
      />
      <span>max</span>
    </>
  ) : (
    <InputNumber disabled={!isEditing} style={{ margin: 10 }} value={value} />
  );
  return (
    <div
      style={{
        display: 'flex',
        alignItems: 'center',
      }}
    >
      <div
        style={{ marginRight: 30, fontWeight: 'bold', color: color || 'black' }}
      >
        {label}
      </div>
      {inputs}
      <div style={{ marginLeft: 20, fontWeight: 'bold' }}>{unit}</div>
    </div>
  );
};

export default IndicatorsForm;
