import React, { useContext } from 'react';
import styled from 'styled-components';

import { BuildingSelect } from '../../../components/BuildingSelect';
import TimeSelect from '../components/TimeSelect';
import CurrentData from '../components/CurrentData';

import { DashboardContext } from '../DashboardDataProvider';
import { AppContext } from '../../../AppDataProvider';

import {
  getBuildingActual,
  getBuildingData,
  getBuildingTime,
} from '../services/api';

export default () => {
  const {
    currentBuilding,
    currentBuildingId,
    currentTime,
    getBuilding,
    startFetchingActual,
    startFetchingData,
    startFetchingTime,
    endFetchingActual,
    endFetchingData,
    endFetchingTime,
    setCurrentBuilding,
    setCurrentTime,
  } = useContext(DashboardContext);

  const building = currentBuilding();

  const updateTime = async ({ target }) => {
    const { value } = target;
    if (!building[value]) {
      fetchBuildingTime(currentBuildingId, value);
    }
    setCurrentTime(value);
  };
  const updateBuilding = async id => {
    const selected = getBuilding(id);
    fetchBuildingActual(id);
    if (!selected.data) {
      fetchBuildingData(id);
    }
    if (!selected[currentTime]) {
      fetchBuildingTime(id, currentTime);
    }
    setCurrentBuilding(id);
  };

  async function fetchBuildingActual(id) {
    startFetchingActual(id);
    const resp = await getBuildingActual(id);
    endFetchingActual(id, resp.data);
  }

  async function fetchBuildingData(id) {
    startFetchingData(id);
    const resp = await getBuildingData(id);
    endFetchingData(id, resp.data);
  }

  async function fetchBuildingTime(id, time) {
    startFetchingTime(id, time);
    const resp = await getBuildingTime(id, time);
    endFetchingTime(id, resp.data, time);
  }

  const { buildings, weatherStationName } = useContext(AppContext);
  const fetchingActual = !building.finished.currentData;
  return (
    <TopBar>
      <BuildingSelectContainer>
        <BuildingSelect
          buildings={buildings}
          onChange={updateBuilding}
          value={currentBuildingId}
        />
      </BuildingSelectContainer>
      <TimeSelectContainer>
        <TimeSelect onChange={updateTime} currentTime={currentTime} />
      </TimeSelectContainer>
      <CurrentDataContainer>
        <CurrentData
          weatherStationName={weatherStationName}
          buildingData={building.currentData}
          loading={fetchingActual}
        />
      </CurrentDataContainer>
    </TopBar>
  );
};

const TimeSelectContainer = styled.div`
  padding: 0 24px;
`;

const CurrentDataContainer = styled.div`
  position: absolute;
  right: 20px;
  bottom: 16px;
  @media (max-width: 1600px) {
    bottom: 20px;
  }
  @media (max-width: 1440px) {
    bottom: 24px;
  }
`;

const BuildingSelectContainer = styled.div`
  width: 248px;
`;

const TopBar = styled.div`
  position: relative;
  height: 80px;
  padding: 24px 16px;
  display: flex;
  @media (max-height: 800px) {
    height: 64px;
    padding: 16px;
  }
`;
