import sleep from '../../../sleep';

// importy z mockw
const report = require('./mocks/report.json');

// Prawdopodobnie trzeba bedzie przeszlac jakis token, jeszcze nie wiem jak, jako argument?
// Trzeba chyba bdzie zrobi getem
export const getReportData = async (config) => {
  await sleep(1000);
  const fake = fakeData(config);
  return {
    status: 200,
    data: {
      ...fake,
      chartsData: fake.buildings.map(({id}) => ({
        id,
        data: report,
      })),
    },
  }
};

const fakeData = (config) => ({
  config,
  community: {
      id: 223,
      name: 'miw',
  },
  buildings: [
      {
          id: 2,
          name: "Szkoa podstawowa nr 23",
      },
      {
          id: 4,
          name: "Senatorium dla marynarzy",
      },
      {
          id: 12,
          name: "Zakad penitarny",
      },
  ],
});
