import React from 'react';
import styled from 'styled-components';

import MathParser from '../../../components/MathParser';
import { formatNumber } from '../../../utils/formaters';
import CardValue from './CardValue';

export default ({buildingData, loading}) => {
    const getValue = ({key, value}) => loading || value || buildingData[key];
    const getData = ({key, value, valueFormatter}) =>
        ((v) => valueFormatter ? (valueFormatter(v)) : v)(getValue({key, value}));

    return (
        <BuildingData>
            {
                loading ?
                elements.map(element =>
                    <MyRow key={element.key}>
                        {element.formatter({
                            loading,
                            label: element.label,
                            unit: element.unit,
                        })}
                    </MyRow>,
                )
                :
                elements.map(element =>
                    <MyRow key={element.key}>
                        {element.formatter({
                            value: getData(element),
                            label: element.label,
                            unit: element.unit,
                        })}
                    </MyRow>,
                )
            }
        </BuildingData>
    );
};

const renderElement = ({value, label, unit, ...props}) =>
    <CardValue
        label={label}
        value={value}
        unit={unit}
        {...props}
    />

const elements = [
    {
        key: "heatConsumption",
        label: "Zuycie ciepa",
        unit: 'kWh',
        valueFormatter: (value) => (`${formatNumber(value, {position: 2})}`),
        formatter: renderElement,
    },
    {
        key: "electricityConsumption",
        label: "Zuycie energii elektrycznej",
        unit: 'kWh',
        valueFormatter: (value) => (`${formatNumber(value, {position: 2})}`),
        formatter: renderElement,
    },
    {
        key: "CO2",
        label: <MathParser>Emisja CO_2</MathParser>,
        unit: 'ton',
        valueFormatter: (value) => (`${formatNumber(value)}`),
        formatter: renderElement,
    },
    {
        key: "OZE",
        label: "Produkcja z OZE",
        unit: 'kWh',
        valueFormatter: (value) => (`${formatNumber(value, 1)}`),
        formatter: renderElement,
    },
    {
        key: "savings",
        label: "Oszczdnoci z OZE",
        unit: "PLN",
        valueFormatter: (value) => (`${value.toFixed(2)}`),
        formatter: renderElement,
    },
];

const BuildingData = styled.div`
    padding: 8px;
`;

const MyRow = styled.div`
    & + & {
        margin-top: 16px;
        @media (max-height: 800px) {
            margin-top: 8px;
        }
    }
`;
