import React, { useState, useContext } from 'react';
import { Button, Checkbox, Select } from 'antd';

import TextField from '../../../../components/CRUDs/TextField';
import SelectField from '../../../../components/CRUDs/SelectField';
import { AppContext } from '../../../../AppDataProvider';
import { AdministrationContext } from '../../AdministrationDataProvider';

const BuildingForm = ({
  editable,
  initialState,
  actionLabel,
  onCancel,
  onAction,
  removeMode = false,
}) => {
  const data = initialState || {
    fullName: '',
    shortName: '',
    street: '',
    city: '',
    postOffice: '',
    hasMetersInstalled: false,
    category: 1,
    userId: null,
  };

  const { enums } = useContext(AppContext);
  const { users } = useContext(AdministrationContext);

  const [formData, setFormData] = useState(data);

  const onChange = (fieldName, e) => {
    setFormData({
      ...formData,
      [fieldName]: e.target.value,
    });
  };

  const handleMetersCheck = e =>
    setFormData({
      ...formData,
      hasMetersInstalled: e.target.checked,
    });

  const handleAction = () => {
    onAction(formData);
    onCancel();
  };

  const handleUserSelect = userId =>
    setFormData({
      ...formData,
      userId,
    });

  const handleCategorySelect = categoryId =>
    setFormData({
      ...formData,
      category: categoryId,
    });

  const textProps = {
    editable,
    onChange,
    formData,
  };

  return (
    <div>
      {removeMode ? (
        <div>Czy na pewno chcesz usun budynek ? </div>
      ) : (
        <>
          <TextField {...textProps} name="fullName" label="Pena Nazwa" />
          <TextField {...textProps} name="shortName" label="Skrt" />
          <TextField {...textProps} name="street" label="Ulica" />
          <TextField {...textProps} name="city" label="Miejscowo" />
          <TextField {...textProps} name="postalCode" label="Kod pocztowy" />
          <TextField {...textProps} name="postOffice" label="Poczta" />
          <Checkbox
            onChange={handleMetersCheck}
            checked={formData.hasMetersInstalled}
            disabled={!editable}
          >
            Budynek opomiarowany
          </Checkbox>
          <SelectField
            placeholder="Wybierz kategori"
            onChange={handleCategorySelect}
            disabled={!editable}
            value={formData.category}
            items={enums.buildingTypes}
          />
          <SelectField
            placeholder="Wybierz uytkownika"
            onChange={handleUserSelect}
            disabled={!editable}
            value={formData.userId}
            items={users.filter(user => user.roleId === 2)}
          />
        </>
      )}
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          marginTop: '20px',
          padding: '0 30px',
        }}
      >
        <Button onClick={onCancel}>Anuluj</Button>
        <Button type="primary" onClick={handleAction}>
          {actionLabel}
        </Button>
      </div>
    </div>
  );
};

export default BuildingForm;
