import React, {useContext, useEffect, useState} from 'react';
import styled from 'styled-components';
import moment from 'moment';

import BuildingDetails from './scenes/BuildingDetails';
import Charts from './scenes/Charts';
import TopBar from './scenes/TopBar';

import {
  getBuildingActual,
  getBuildingData,
  getBuildingTime,
} from './services/api';

import { DashboardContext } from './DashboardDataProvider';
import { AppContext } from '../../AppDataProvider';

export default () => {
  const {
    currentTime,
    getCurrentTime,
    currentBuildingId,
    currentBuilding,
    setBuildings,
    // Pobierane danych
    endFetchingActual,
    endFetchingData,
    endFetchingTime,
    depricateHourData,
    depricateDailyData,
  } = useContext(DashboardContext);
  const { buildings } = useContext(AppContext);
  const [dailyInterval, setDailyInterval] = useState(null);

  useEffect(() => {
    fetchBuildings();
    const actualInterval = setInterval(
      () => {
        resetHourData();
      }, 1000 * 60 * 60);
    const dailyStart = setTimeout(
      () => {
        depricateDailyData();
        resetDailyData()
        setDailyInterval(setInterval(
          () => {
            depricateDailyData();
            resetDailyData();
          }, 1000 * 60 * 60 * 24));
      }, 1000 * secondsToMidnight());

      return () => {
        clearInterval(actualInterval);
        clearTimeout(dailyStart);
        if(dailyInterval) {
          try {
            clearInterval(dailyInterval);
          }
          catch {
            console.warn('Pusty interval dzienny wykryty');
          };
        }
      }
  }, []);

  return (
  <Dashboard>
    <TopBar />
    <DashboardInner>
      <BuildingDetails />
      <ChartsContainer>
        <Charts />
      </ChartsContainer>
    </DashboardInner>
  </Dashboard>
  );

  async function resetHourData() {
    const building = currentBuilding();
    if( building.id !== null ) {
      depricateHourData();
      fetchBuildingActual(building.id);
      const time = getCurrentTime();
      if(['day', 'week'].includes(time)) {
        fetchBuildingTime(building.id, time);
      }
    }
  }

  async function resetDailyData() {
    const building = currentBuilding();
    if( building.id !== null ) {
      depricateDailyData();
      fetchBuildingData(building.id);
      const time = getCurrentTime();
      if(['month', 'year'].includes(time)) {
        fetchBuildingTime(building.id, time);
      }
    }
  }

  async function fetchBuildings() {
    if(currentBuildingId !== null) {
      fetchBuildingActual(currentBuildingId);
    }
    else {
      const { id } = buildings[0];
      setBuildings(buildings, id);
      fetchBuildingActual(id);
      fetchBuildingData(id);
      fetchBuildingTime(id, currentTime);
    }
  }

  async function fetchBuildingActual(id) {
    const resp = await getBuildingActual(id);
    endFetchingActual(id, resp.data);
  }

  async function fetchBuildingData(id) {
    const resp = await getBuildingData(id);
    endFetchingData(id, resp.data);
  }

  async function fetchBuildingTime(id, time) {
    const resp = await getBuildingTime(id, time);
    endFetchingTime(id, resp.data, time);
  }
}

const secondsToMidnight = () => {
  const mmt = moment();
  const mmtMidnight = mmt.clone().endOf('day');
  return mmtMidnight.diff(mmt, 'seconds') + 1;
}


const Dashboard = styled.div`
  background: #EEE;
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  height: calc(100vh - 56px);
  min-height: 664px;
  width: calc(100vw - 160px);
  min-width: 1206px;
`;

const DashboardInner = styled.div`
  display: flex;
  flex: 1;
  position: relative;
`;

const ChartsContainer = styled.div`
  flex: 1;
  position: relative;
`;
