import React, { useState } from 'react';
import { Input, Button, Form, Checkbox } from 'antd';
import styled from 'styled-components';

import TextField, {
  formItemLayout,
} from '../../../../components/CRUDs/TextField';

const ROLES = {
  USER: 2,
  PROVIDER: 3,
};

const UserForm = ({
  editable,
  initialState,
  actionLabel,
  onCancel,
  onAction,
  removeMode = false,
}) => {
  const data = initialState || {
    login: '',
    name: '',
    reportEmail: '',
    contactPerson: '',
    contactPhone: '',
    address: '',
    appHeader: '',
    weatherStationName: '',
    roleId: ROLES.USER,
  };

  const createMode = editable && !initialState;

  const [formData, setFormData] = useState(data);
  const [isPasswordValid, setIsPasswordValid] = useState(true);

  const onChange = (fieldName, e) => {
    setFormData({
      ...formData,
      [fieldName]: e.target.value,
    });
  };

  const passwordRegexp = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{6,}$/;

  const handleAction = () => {
    const isValid = passwordRegexp.test(formData.password);
    setIsPasswordValid(isValid);
    if (isValid || !createMode) {
      onAction(formData);
      onCancel();
    }
  };

  const handleRoleCheck = e => {
    const roleId = e.target.checked ? ROLES.PROVIDER : ROLES.USER;
    setFormData({
      ...formData,
      roleId,
    });
  };

  const handlePassword = e => {
    const newValue = e.target.value;
    setFormData({
      ...formData,
      password: newValue,
    });
    if (passwordRegexp.test(newValue)) {
      setIsPasswordValid(true);
    }
  };

  const textProps = {
    editable,
    formData,
    onChange,
  };

  return (
    <div>
      {removeMode ? (
        <div>Czy na pewno chcesz usun uytkownika ? </div>
      ) : (
        <React.Fragment>
          <TextField {...textProps} name="login" label="Login" />
          {createMode && (
            <Form.Item {...formItemLayout} label="Haso">
              <Input.Password
                value={formData.password}
                onChange={handlePassword}
              />
            </Form.Item>
          )}
          {!isPasswordValid && (
            <div
              style={{
                margin: 5,
                color: '#dd2222',
                marginLeft: 25,
                marginTop: -20,
              }}
            >
              Haso musi mie co najmniej 6 znakw, ma liter, du liter i
              cyfr
            </div>
          )}
          <TextField {...textProps} name="name" label="Nazwa klienta" />
          <TextField {...textProps} name="reportEmail" label="E-mail" />
          <TextField
            {...textProps}
            name="contactPerson"
            label="Osoba do kontaktu"
          />
          <TextField {...textProps} name="contactPhone" label="Telefon" />
          <TextField {...textProps} name="address" label="Adres klienta" />
          <TextField
            {...textProps}
            name="appHeader"
            label="Nagwek aplikacji"
          />
          <TextField
            {...textProps}
            name="weatherStationName"
            label="Nazwa stacji pogodowej"
          />
          <Checkbox
            onChange={handleRoleCheck}
            checked={formData.roleId === ROLES.PROVIDER}
          >
            Dostawca danych
          </Checkbox>
        </React.Fragment>
      )}
      <DeleteContainer>
        <Button onClick={onCancel}>Anuluj</Button>
        <Button type="primary" onClick={handleAction}>
          {actionLabel}
        </Button>
      </DeleteContainer>
    </div>
  );
};

const DeleteContainer = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  padding: 0 30px;
`;

export default UserForm;
