import React from 'react';

import chartConfig from './chartsConfig';

import ChartHolder, { ChartHolderItem } from '../components/ChartHolder';
import Conclusion from '../components/Conclusion';
import CustomChart from '../../../components/CustomChart';
import Page from '../components/Page';
import PageHeading from '../components/PageHeading';
import { genReportLink } from '../components/ReportNavigation';

export default ({building, data, comments = {}, ...props}) => (<Page {...props}>
    <PageHeading
        id={genReportLink(building.id)}
        to="Navigation"
    >
        {building.name}
    </PageHeading>
    <ChartHolder>
        <WrappedChart {...passedChartConfig(data, "dailyHeatConsumption")}/>
        <WrappedChart {...passedChartConfig(data, "dailyElectricityConsumption")}/>
    </ChartHolder>
    <Conclusion
        comment={comments.dailyHeatConsumption}
        refId={`${building.id}-dailyHeatConsumption`}
    />
    <ChartHolder>
        <WrappedChart {...passedChartConfig(data, "comparedConsumption")}/>
        <WrappedChart {...passedChartConfig(data, "heatingCurve")}/>
        <WrappedChart {...passedChartConfig(data, "energyUsageAreas")}/>
    </ChartHolder>
    <Conclusion
        comment={comments.comparedConsumption}
        refId={`${building.id}-comparedConsumption`}
    />
    <ChartHolder>
        <WrappedChart {...passedChartConfig(data, "dailyOZEProduction")}/>
    </ChartHolder>
    <Conclusion
        comment={comments.dailyOZEProduction}
        refId={`${building.id}-dailyOZEProduction`}
    />
</Page>);

const WrappedChart = ({chartSize, ...props}) =>
    (<ChartHolderItem size={chartSize}>
        <CustomChart {...props} />
    </ChartHolderItem>);

const passedChartConfig = (data, key) =>
({
    data: data[key].data,
    ...chartConfig[key](data[key].data),
});
