import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

import BuildingReport from './scenes/BuildingReport';
import { getReportData } from './services/api';

export default (props) => {
const {match: {params}} = props;
const {building, year, quarter} = params;

const [data, updateData] = useState();

useEffect(() => {
    loadData(`${building}-${year}-${quarter}`);
}, []);

const loadData = async (passedId) => {
    const resp = await getReportData(passedId);
    updateData(resp.data);
}
return (
    data ?
(<StyledReportsGenerator id="Result">
 <BuildingReport loadedData={data} />
 </StyledReportsGenerator>)
: null);

}

const StyledReportsGenerator=styled.div`
@media print {
    @page {
        margin: 2.5cm;
        font-size: 12pt;
    }
}
`;
