import React from 'react';
import styled from 'styled-components';

import ReportNavigation from '../components/ReportNavigation';
import BuildingReport from './BuildingReport';
import FrontPage from '../components/FrontPage';
import Summary from './Summary';

export default ({
    community,
    buildings,
    chartsData,
    printRef,
    comments = {},
    ...props
}) => (
    <StyledReportGeneration id="ReportGeneration" {...props}>
        <ReportInner>
            <ReportContent ref={printRef}>
                <FrontPage name={community.name} />
                <ReportNavigation
                    community={community}
                    buildings={buildings}
                />
                {buildings.map(building =>(
                    <BuildingReport
                        comments={comments[building.id]}
                        key={building.id}
                        building={building}
                        data={chartsData.find(({id}) => id === building.id).data}
                    />))}
                <Summary comments={comments}/>
            </ReportContent>
        </ReportInner>
    </StyledReportGeneration>
);

const StyledReportGeneration = styled.div`
    position: relative;
    background: #EEE;
    margin: 0 -20px;
    padding: 24px 0;
`;

const ReportInner = styled.div`
    position: relative;
    margin: auto;
    max-width: 1224px;
    background: white;
    min-height: 1600px;
`;

const ReportContent = styled.div`
    padding: 24px 0;
    width: 976px;
    margin: auto;
`;
