import React, { useContext } from 'react';
import styled from 'styled-components';

import { AnalysisContext } from './AnalysisDataProvider';
import { PHASES } from './constants';

import ChartCreate from './scenes/ChartCreate';
import ChartTypeSelect from './scenes/ChartTypeSelect';

export default () => {
  const {
    phase,
    setChartType,
    setPhase,
  } = useContext(AnalysisContext)

  return (
  <Analysis>
    {
      ({
        [PHASES.FETCHING]: <p>Pobieramy dane</p>,
        [PHASES.INITIAL]: <ChartTypeSelect onSelect={setChartType} />,
        [PHASES.CREATE]: <ChartCreate />,
      })[phase]
    }
  </Analysis>
  );

}

const Analysis = styled.div`
  background: #EEE;
  height: 100%;
  flex: 1;
`;
