import React from 'react';
import styled from 'styled-components';
import { Icon } from 'antd';

export default ({type, setType, error}) =>
(<ChartSelect>
    <MainTitle>Wybr rodzaju wykresu</MainTitle>
    <ChartsToSelect>
        {selectConfig.map(({key, icon, text}) =>
            <ChartOption
                key={key}
                icon={icon}
                text={text}
                active={type === key}
                onSelect={() => setType(key)}
            />,
        )}
    </ChartsToSelect>
    {
        (e => e ? (
            <Description>
                <Title>{e.text}</Title>
                {e.description}
            </Description>
        ) : null)(selectConfig.find(({key}) => key === type))
    }
    {
        error &&
        <Description error>
            Aby kontunuowa, prosz wybra rodzaj wykresu do utworzenia.
        </Description>
    }
</ChartSelect>);

const selectConfig = [
    {
        key: 'line',
        icon: 'line-chart',
        text: 'Wykres liniowy',
        description: 'Na wykresie liniowym przedstawiane s dane odniesione do czasu. Przedstawiaj dane zbierane bierzco przez liczniki.',
    },
    {
        key: 'bar',
        icon: 'bar-chart',
        text: 'Wykres supkowy',
        description: 'Na wykresie supkowym przedstawiane s dane zbiorcze z licznikw. Dane zbierane s w wybranych okresach czasowych. Wyniki dostpne s z listy dostpnych wskanikw.',
    },
    {   key: 'pie',
        icon: 'pie-chart',
        text: 'Wykres koowy',
        description: 'Na wykresie koowym przedstawione mog by wskaniki oparte o te same jednostki. Wybr danych ograniczony jest do listy wskanikw.',
    },
];

const ChartOption = ({icon, text, active, onSelect}) => (
    <Charts>
        <ChartType active={active} onClick={onSelect}>
            <StyledIcon type={icon}/>
            <IconDescription>
                {text}
            </IconDescription>
        </ChartType>
    </Charts>
);

const IconDescription = styled.div`
    margin: auto;
    font-size: 16px;
    line-height: 18px;
    padding-top: 6px;
`;

const ChartSelect = styled.div`
    width: 720px;
    height: 280px;
    padding-bottom: 8px;
`;

const ChartsToSelect = styled.div`
    text-align: center;
`;

const Charts = styled.div`
    display: inline-block;
    vertical-align: top;
    padding: 16px 4px;
`;

const ChartType = styled.div`
    position: relative;
    height: 120px;
    width: 160px;
    padding: 12px;
    margin: auto;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.5s ease;
    ${({active}) => active ? `
        background: #1890ff;
        color: #FFF;
        &:after {
            content: '';
            position: absolute;
            left: 50%;
            bottom: 0%;
            width: 0;
            height: 0;
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            border-top: 8px solid #1890ff;
            clear: both;
            transform: translate(-8px, 8px);
        }
    ` : ''}
    &:hover {
        background: #1890ff;
        color: #FFF;
    }
`;

const StyledIcon = styled(Icon)`
    margin-top: 8px;
    font-size: 48px;
    text-align: center;
    transition: font-size 0.5s ease, margin-top 0.5s ease;
    ${ChartType}:hover & {
        font-size: 56px;
        margin-top: 0;
    }
`;

const Description = styled.div`
    ${({error}) => error ?
        `background: #FFAAAA;
        color: #801515;`
        :
        `background: #EEE;
        color: #333;`
    }
    padding: 16px;
    text-align: justify;
    font-size: 1rem;
    line-height: 1.625rem;
`;

const MainTitle = styled.div`
    font-size: 1.5rem;
    line-height: 2.5rem;
    text-align: center;
    margin-bottom: 12px;
    font-weight: bold;
`;

const Title = styled.div`
    font-size: 1.25rem;
    line-height: 2rem;
`;
