import moment from 'moment';

import sleep from '../../../sleep';

// generator
const RandomSerie = ({
  from = 10,
  to = 15,
  wavePeriod = 1,
  waveScale = 0,
  rand = 0.2,
  dataSets = 50,
} = {}) => {
  const result = [];
  for (let i = 0; i < dataSets; i+=1) {
    const generated =
      from +
      ((to - from) * i) / (dataSets - 1) +
      Math.sin(((i / (dataSets - 1)) * 2 * Math.PI) / wavePeriod) * waveScale +
      rand * Math.random();
    result.push(parseFloat(generated.toFixed(2)));
  }
  return result;
};

const DataSetLabel = ({
  dataSets = 50,
  day = null,
  add = 4,
  start = null,
  type = "hours",
} = {}) => {
  const result = [];
  const dbegin = day || Math.round(10 + 1 * Math.random());
  const begin = moment(start || `2019-03-${dbegin}`);
  for (let i = 0; i < dataSets; i+=1) {
    result.push({
      time: parseFloat(begin.format("x")),
    });
    begin.add(add, type);
  }
  return result;
};

const GenerateData = (keys) => {
  const result = DataSetLabel();
  keys.forEach(key => {
    const data = RandomSerie();
    data.forEach((val, i) => {
      result[i][key] = val;
    });
  });
  return result;
}

export const getLineChartData = async (request) => {
  await sleep(1500);
  return {
    status: 200,
    data: GenerateData(request.measurements),
  }
}
