import React, { useContext } from 'react';
import styled from 'styled-components';

import AnnualData from '../components/AnnualData';
import CommunityCompare from '../components/CommunityCompare';
import TimeSection from '../components/TimeSection';

import { DashboardContext } from '../DashboardDataProvider';

export default () => {
    const { currentBuilding } = useContext(DashboardContext);
    const building = currentBuilding();
    const fetchingData = !building.finished.data;
    return (
        <BuildingDetails>
            <CommunityCompare data={building.compare} loading={fetchingData} />
            <TimeSection startTime={building.startTime} loading={fetchingData} />
            <AnnualData buildingData={building.data} loading={fetchingData} />
        </BuildingDetails>
    );
};

const BuildingDetails = styled.div`
    width: 272px;
    padding: 0 16px;
    @media (max-width: 1600px) {
        width: 236px;
        padding: 0 12px;
    }
    @media (max-width: 1440px) {
        width: 200px;
        padding: 0 8px;
    }
`;
