import React, { useContext } from 'react';
import { Modal } from 'antd';

import { AdministrationContext } from '../AdministrationDataProvider';
import UserForm from './Users/UserForm';
import BuildingForm from './Buildings/BuildingForm';

const CommonModal = () => {
  const {
    // Buildings
    createBuilding,
    updateBuilding,
    deleteBuilding,
    // Users
    createUser,
    updateUser,
    deleteUser,
    // Rest
    currentEntry,
    setMode,
    setKind,
    currentConfig,
    setIsModalVisible,
    isModalVisible,
  } = useContext(AdministrationContext);

  const closeModal = () => {
    setIsModalVisible(false);
    setMode(undefined);
    setKind(undefined);
  };

  const userModalConfigs = {
    createUsers: {
      header: 'Nowy Uytkownik',
      component: () => (
        <UserForm
          editable
          actionLabel="Stwrz"
          onAction={createUser}
          onCancel={closeModal}
        />
      ),
    },
    readUsers: {
      header: 'Szczegoy Uytkownika',
      component: () => (
        <UserForm
          actionLabel="Ok"
          initialState={currentEntry}
          onAction={closeModal}
          onCancel={closeModal}
        />
      ),
    },
    updateUsers: {
      header: 'Edytuj Uytkownika',
      component: () => (
        <UserForm
          editable
          actionLabel="Zapisz"
          initialState={currentEntry}
          onAction={updateUser}
          onCancel={closeModal}
        />
      ),
      actionLabel: 'Zapisz',
    },
    deleteUsers: {
      header: 'Usu uytkownika',
      component: () => (
        <UserForm
          removeMode
          actionLabel="Usu"
          initialState={currentEntry}
          onAction={deleteUser}
          onCancel={closeModal}
        />
      ),
    },
  };

  const buildingModalConfigs = {
    createBuildings: {
      header: 'Nowy Obiekt',
      component: () => (
        <BuildingForm
          editable
          actionLabel="Stwrz"
          onAction={createBuilding}
          onCancel={closeModal}
        />
      ),
    },
    readBuildings: {
      header: 'Szczegoy Obiektu',
      component: () => (
        <BuildingForm
          actionLabel="Ok"
          initialState={currentEntry}
          onAction={closeModal}
          onCancel={closeModal}
        />
      ),
    },
    updateBuildings: {
      header: 'Edytuj Obiekt',
      component: () => (
        <BuildingForm
          editable
          actionLabel="Zapisz"
          initialState={currentEntry}
          onAction={updateBuilding}
          onCancel={closeModal}
        />
      ),
      actionLabel: 'Zapisz',
    },
    deleteBuildings: {
      header: 'Usu Obiket',
      component: () => (
        <BuildingForm
          removeMode
          actionLabel="Usu"
          initialState={currentEntry}
          onAction={deleteBuilding}
          onCancel={closeModal}
        />
      ),
    },
  };
  const modalConfigs = {
    ...userModalConfigs,
    ...buildingModalConfigs,
  };

  const currentModalConfig = modalConfigs[currentConfig];
  const ModalComponent = currentModalConfig
    ? currentModalConfig.component
    : null;

  return currentModalConfig ? (
    <Modal
      title={currentModalConfig.header}
      visible={isModalVisible}
      footer={null}
      onCancel={closeModal}
      cancelText="Anuluj"
    >
      <ModalComponent />
    </Modal>
  ) : null;
};

export default CommonModal;
