import React from 'react';
import styled from 'styled-components';

import { formatNumber } from '../../../utils/formaters';
import ScatteredChart from '../../../components/Charts/ScatteredChart';

const exampleConfig = {
    chartConfig: {
      settings: {
        stopAnimation: true,
        stopInteractive: true,
        title: "",
        showTitle: false,
        showLegend: false,
        horizontalGrid: true,
        verticalGrid: false,
        stackData: true,
        yFormatter: (e) => formatNumber(e, {position: -1}),
      },
      xAxis: {
        xKey: "temperature",
        description: "Temperatura",
        showDescription: true,
        color: "#333",
        datesNumber: 3,
        withTime: false,
        rangeFrom: "auto",
        rangeTo: "auto",
        rangeSpan: "auto",
      },
      yAxises: [
        {
          id: "0",
          color: "#333",
          showAxis: true,
          side: "left",
          description: "Zuycie ciepa",
          showDescription: true,
          unit: "kWh",
          rangeFrom: "0",
          rangeTo: "auto",
          rangeSpan: "auto",
        },
      ],
    },
    usedData: [
      {
        id: "0",
        descriptionId: "building",
        showDescription: true,
        dataSource: "dailyHeatConsumption",
        radius: 3,
        axis: "0",
        lineWidth: 2,
        lineColor: "#063951",
        unit: "kWh",
      },
    ],
};

const exampleData = (len = 100) => Array(len).fill(0).map((e, i) => ({
    temperature: -10 + 32 * Math.random(),
    dailyHeatConsumption: 28 * Math.random() + 15,
    building: `SP ${Math.floor(100 * Math.random() + 1)}`,
}));

export default ({length}) => (
  <StyledExample>
      <ScatteredChart data={exampleData(length)} {...exampleConfig} />
  </StyledExample>
);

const StyledExample = styled.div`
    height: 540px;
    width: 900px;
`;
