import React from 'react';

import { Modal } from 'antd';

export default ({visible, onOk, onCancel, ...props}) =>
    (<Modal 
        visible={visible}
        onOk={onOk}
        onCancel={onCancel}
        title="Czy chcesz wczyta dane"
        {...props}
    >
        {`Wczytanie nowych danych spowoduje przeadowanie generacji raportu.
        Przy braku zapisu spowoduje to utrat wykonanej pracy.`}
    </Modal>);