import React from 'react';
import { Form, InputNumber, Tag } from 'antd';

const NumberField = ({ name, label, formData, onChange, editable }) => {
  return (
    <Form.Item {...formItemLayout} label={label}>
      <CrudInputNumber
        value={formData[name]}
        onChange={e => onChange(name, e)}
        editable={editable}
      />
    </Form.Item>
  );
};

const CrudInputNumber = ({ value, onChange, editable, ...rest }) => {
  return editable ? (
    <InputNumber value={value} onChange={onChange} {...rest} />
  ) : (
    <Tag style={{ pointerEvents: 'none', width: '100%' }} {...rest}>
      {value || 'brak'}
    </Tag>
  );
};

export const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 8 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 },
  },
};

export default NumberField;
