import React from 'react';
import styled from 'styled-components';

import Loading from '../../../components/Loading';

export default ({data, loading}) =>
    <CommunityCompare>
        <InnerContent>
            {loading
            ? <Loading color="blue"/>
            : "Tutaj pojawi si porwnanie w gminie"}
        </InnerContent>
    </CommunityCompare>

const InnerContent = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    padding: 4px;
`;

const CommunityCompare = styled.div`
    position: relative;
    padding-top: 50%;
    background: white;
    margin-top: 8px;
    @media (max-height: 800px) {
        margin-bottom: 4px;
    }
    font-size: 1rem;
    @media (max-width: 1600px) {
        font-size: 0.9em;
    }
    @media (max-width: 1440px) {
        font-size: 0.8em;
    }
`;

