import React, { useState, useContext } from 'react';

import { AppContext } from '../../AppDataProvider';

import { getActiveAlarms, getAlarmsHistory } from './services/alarms';
import {
  getAllUsersActiveAlarms,
  getAllUsersAlarmsHistory,
  getAllUsersAlarmsSettings,
  postChangeSettingTreshold,
  postToggleIsSettingWatched,
} from './services/allUsersAlarms';

const AlarmsContext = React.createContext({});

const AdministrationDataProvider = ({ children }) => {
  const { startAsyncTask, finishAsyncTask } = useContext(AppContext);

  const [activeAlarms, setActiveAlarms] = useState([]);
  const [alarmsHistory, setAlarmsHistory] = useState([]);
  const [allUsersActiveAlarms, setAllUsersActiveAlarms] = useState([]);
  const [allUsersAlarmsHistory, setAllUsersAlarmsHistory] = useState([]);
  const [allUsersAlarmsSettings, setAllUsersAlarmsSettings] = useState([]);

  async function fetchActiveAlarms() {
    startAsyncTask();
    const resp = await getActiveAlarms();
    finishAsyncTask();
    setActiveAlarms(resp.alarms);
  }

  async function fetchAlarmsHistory() {
    startAsyncTask();
    const resp = await getAlarmsHistory();
    finishAsyncTask();
    setAlarmsHistory(resp.history);
  }

  async function fetchAllUsersActiveAlarms() {
    startAsyncTask();
    const resp = await getAllUsersActiveAlarms();
    finishAsyncTask();
    setAllUsersActiveAlarms(resp.alarms);
  }

  async function fetchAllUsersAlarmsHistory() {
    startAsyncTask();
    const resp = await getAllUsersAlarmsHistory();
    finishAsyncTask();
    setAllUsersAlarmsHistory(resp.history);
  }

  async function fetchAllUsersAlarmsSettings() {
    startAsyncTask();
    const resp = await getAllUsersAlarmsSettings();
    finishAsyncTask();
    setAllUsersAlarmsSettings(resp.settings);
  }

  function toggleIsSettingWatched(userId, buildingId, name, value) {
    postToggleIsSettingWatched(userId, buildingId, name, value);
  }

  function changeSettingTreshold(userId, buildingId, name, value) {
    postChangeSettingTreshold(userId, buildingId, name, value);
  }

  const state = {
    activeAlarms,
    alarmsHistory,
    allUsersActiveAlarms,
    allUsersAlarmsHistory,
    allUsersAlarmsSettings,

    fetchActiveAlarms,
    fetchAlarmsHistory,
    fetchAllUsersActiveAlarms,
    fetchAllUsersAlarmsHistory,
    fetchAllUsersAlarmsSettings,

    toggleIsSettingWatched,
    changeSettingTreshold,
  };

  return (
    <AlarmsContext.Provider value={state}>{children}</AlarmsContext.Provider>
  );
};

export { AlarmsContext };
export default AdministrationDataProvider;
