import React from 'react';
import { Table, Button } from 'antd';
import { format } from 'date-fns';

const allBuildings = ['PSP 17 Radom', 'PSP 23 Radom'];
const allUsers = ['Mateusz Michalewski', 'Adam Gryz', 'Micha Dzidziewicz'];

const formatDate = date => format(date, 'HH:mm DD/MM/YYYY ');

const baseColumns = [
  {
    title: 'Budynek',
    dataIndex: 'building',
    filters: allBuildings.map(building => ({
      text: building,
      value: building,
    })),
    onFilter: (value, record) => record.building === value,
  },
  {
    title: 'Data, godzina',
    dataIndex: 'time',
    defaultSortOrder: 'descend',
    sorter: (a, b) => a.time - b.time,
    render: date => formatDate(date),
  },
  {
    title: 'Rodzaj alarmu',
    dataIndex: 'alarmType',
  },
  {
    title: 'Wskazanie',
    dataIndex: 'value',
  },
  {
    title: 'Prg',
    dataIndex: 'treshold',
  },
];

const columnsWithUsers = [
  {
    title: 'Uytkownik',
    dataIndex: 'user',
    filters: allUsers.map(user => ({
      text: user,
      value: user,
    })),
    onFilter: (value, record) => record.user === value,
  },
  ...baseColumns,
];

const historyColumns = [
  {
    title: 'Data wyczenia',
    dataIndex: 'offTime',
    render: date => formatDate(date),
  },
];

const activeAlarmsColumns = [
  {
    title: 'Akcja',
    dataIndex: 'id',
    render: id => (
      <Button type="danger" onClick={() => alert(id)}>
        WYCZ
      </Button>
    ),
  },
];

const AlarmsTable = ({ alarms, isAdmin, isHistory }) => {
  const initial = isAdmin ? columnsWithUsers : baseColumns;
  const columns = isHistory
    ? [...initial, ...historyColumns]
    : [...initial, ...activeAlarmsColumns];
  return (
    <Table
      columns={columns}
      dataSource={alarms}
      style={{ margin: 20 }}
      pagination={{ position: 'none' }}
    />
  );
};

export default AlarmsTable;
