import React, { Fragment } from "react";
import styled from 'styled-components';
import { filter } from "lodash";
import MathParser from "../MathParser";
import { isTimeChart } from "./utils/filters";
import {formatDate, formatHour} from "../../utils/formaters";

export default ({
    active,
    payload,
    label,
    xAxis,
    withTime,
    xFormatter,
    yFormatter,
  }) => {
    const { xKey, description, unit } = xAxis;
    const simpleFormatter = (value) => value;
    const defaultTimeFormatter = (value) => `${formatDate(value)}${withTime ? ` ${formatHour(value)}` : ""}`
    const mainFormatter = xFormatter || (isTimeChart(xKey) ? defaultTimeFormatter : simpleFormatter);
    const dataFormatter = yFormatter || simpleFormatter;
    if (active) {
      return (
        <StyledTooltip>
          {isTimeChart(xKey) ? (
            <MainLabel>
              {mainFormatter(label)}
            </MainLabel>
          ) :
          (
            <MainLabel>
                <MathParser>{`${description} ${mainFormatter(label)} ${unit || ""}`}</MathParser>
            </MainLabel>
          )}
          <Table>
            {filter(payload, e => !e.dataKey.startsWith("REG_")).map((e) => (
              <Property key={e.dataKey} color={e.color}>
                <Feature>
                  <MathParser>{e.name}</MathParser>
                </Feature>
                <FeatureValue>
                  <MathParser>{`${dataFormatter(e.payload[e.dataKey])} ${e.unit}`}</MathParser>
                </FeatureValue>
              </Property>
            ))}
          </Table>
        </StyledTooltip>
      );
    }
    return null;
  };
  
export const PieTooltip = ({
  active,
  payload,
  dataFormatter = (e) => e,
}) => {
  if (active) {
    return (
      <StyledTooltip>
        {payload.map(e => (
          <Fragment key={e.dataKey}>
            <MainLabel>
                <MathParser>{e.payload.description}</MathParser>
            </MainLabel>
            <Table>
                <Property>
                  <FeatureValue>
                    <MathParser>{`${dataFormatter(e.value)} ${e.payload.unit}`}</MathParser>
                  </FeatureValue>
                </Property>
            </Table>
        </Fragment>
      ))}
      </StyledTooltip>
    );
  }
  return null;
};

  const StyledTooltip = styled.div`
    background: white;
    border: solid 1px #d4d4d4;
    border-radius: 4px;
    padding: 8px 8px 16px 8px;
    min-width: 120px;
    display: table;
    box-shadow: 0 0 8px rgba(40, 40, 40, 0.3);
    font-size: 1rem;
    @media (max-width: 1600px) {
        font-size: 0.9em;
    }
    @media (max-width: 1440px) {
        font-size: 0.8em;
    }
  `;

  const Table = styled.div`
    display: table;
  `;
  
  const MainLabel = styled.div`
    font-weight: bold;
  `;
  
  const Property = styled.div`
    display: table-row;
    ${({color}) => color ? `color: ${color}` : ''};
  `;
  
  const Feature = styled.div`
    display: table-cell;
    padding-right: 8px;
  `;
  
  const FeatureValue = styled.div`
    display: table-cell;
    text-align: right;
  `;