import React, { useContext, useEffect } from 'react';
import { Tabs } from 'antd';

import { AlarmsContext } from '../AlarmsDataProvider';
import AlarmsTable from '../components/AlarmsTable';

const UserAlarms = () => {
  const {
    activeAlarms,
    alarmsHistory,
    fetchActiveAlarms,
    fetchAlarmsHistory,
  } = useContext(AlarmsContext);

  useEffect(() => {
    fetchActiveAlarms();
    fetchAlarmsHistory();
  }, []);

  return (
    <div>
      <Tabs>
        <Tabs.TabPane tab="Aktwyne alarmy" key="alarms">
          <AlarmsTable alarms={activeAlarms} />
        </Tabs.TabPane>
        <Tabs.TabPane tab="Historia alarmw" key="history">
          <AlarmsTable alarms={alarmsHistory} isHistory />
        </Tabs.TabPane>
      </Tabs>
    </div>
  );
};

export default UserAlarms;
