import React, { Component } from 'react';

import { PHASES } from './constants';

const initialAnalysisState = {
  phase: PHASES.CREATE,
  chartType: null,
  loadedData: {},
};

const AnalysisContext = React.createContext({});

class AnalysisDataProvider extends Component {
  set = field => value => this.setState(() => ({ [field]: value }));

  setPhase = this.set('phase');

  setChartType = (type) => {
    this.setState(() => ({
      phase: PHASES.CREATE,
      chartType: type,
    }))
  }

  state = {
    ...initialAnalysisState,
    setChartType: this.setChartType,
    setPhase: this.setPhase,
  };

  render() {
    return (
      <AnalysisContext.Provider value={this.state}>
        {this.props.children}
      </AnalysisContext.Provider>
    );
  }
}

export { AnalysisContext };
export default AnalysisDataProvider;
