import React from 'react';

import Page from '../components/Page';
import PageHeading from '../components/PageHeading';
import Conclusion from '../components/Conclusion';

export default ({comments = {}, ...props}) =>
    (<Page {...props}>
        <PageHeading
            id="Summary"
            to="Navigation"
        >
            Wnioski kocowe
        </PageHeading>
        <Conclusion
            comment={comments.summary}
            refId='summary'
        />
    </Page>);
