import React, { useContext } from 'react';
import styled from 'styled-components';
import { filter } from 'lodash';

import { Icon, Select, Empty, TreeSelect } from 'antd';

import { AppContext } from '../AppDataProvider';
import { removePolish } from '../utils/formaters';

const Option = Select.Option;

/*
    Domylana struktura danych:
    communities: [{
        id: id_gminy,
        name: nazwa_gminy <- wyszukiwanie po niej
        buildings: [] <- lista id budynkw w gminie
    }],
    buildings: [{
        id: id_budynku,
        name: nazwa_budynku
    }]
*/

const communityFormatter = id => `community-${id}`;

const buildingsWithCommunities = (communities, buildings) =>
    communities.map(community => ({
        ...community,
        buildings: community.buildings.map(id => buildings.find(e => e.id === id)),
    }));

const buildingDefaultFilterOption = (input, option) =>
option.props.isLeaf ?
    removePolish(option.props.title.toLowerCase()).includes(removePolish(input.toLowerCase()))
    :
    option.props.children.reduce((prev, child) => prev || buildingDefaultFilterOption(input, child), false);

export const BuildingSelect = ({onChange, selectedCommunity, ...props}) => {
    const {buildings, communities} = useContext(AppContext);
    const usedCommunities = selectedCommunity ?
        filter(communities, ({id}) => communityFormatter(id) === selectedCommunity)
        :
        communities;
    return (
        <StyledCommonSelect>
            <StyledIcon 
                type="home"
            />
            <StyledTreeSelect
                showSearch
                placeholder="Wybierz budynek"
                treeDefaultExpandAll
                notFoundContent={
                    <Empty description="Brak wynikw" image={Empty.PRESENTED_IMAGE_SIMPLE}/>
                }
                onChange={onChange}
                filterTreeNode={buildingDefaultFilterOption}
                treeData={buildingsWithCommunities(usedCommunities, buildings).map(e => ({
                    title: e.name,
                    isLeaf: false,
                    value: communityFormatter(e.id),
                    selectable: false,
                    key: communityFormatter(e.id),
                    children: e.buildings.map(({id, name}) => ({
                        title: name,
                        value: id,
                        key: id,
                        isLeaf: true,
                    })),
                }))}
                {...props}
            />
        </StyledCommonSelect>
    );
}

const defaultFilterOption = (input, option) =>
    removePolish(option.props.children.toLowerCase()).includes(removePolish(input.toLowerCase()));

export const CommunitySelect =  ({
    onChange,
    ...props
}) => {
    const {communities} = useContext(AppContext);
    return (
        <StyledCommonSelect>
            <StyledIcon 
                type="compass"
            />
            <StyledSelect
                showSearch
                placeholder="Wybierz gmin"
                notFoundContent={
                    <Empty description="Brak wynikw" image={Empty.PRESENTED_IMAGE_SIMPLE}/>
                }
                onChange={onChange}
                filterOption={defaultFilterOption}
                {...props}
            >
                {communities.map(({id, name}) =>
                    <Option
                        key={communityFormatter(id)}
                        value={communityFormatter(id)}
                    >
                        {name}
                    </Option>,
                )}
            </StyledSelect>
        </StyledCommonSelect>
    );
}

const StyledIcon = styled(Icon)`
    font-size: 24px;
    padding: 4px 12px 4px 0;
    flex: 0;
`;

const StyledSelect = styled(Select)`
    flex: 1 1 216px;
`;

export const StyledCommonSelect = styled.div`
    width: 100%;
    display: flex;
`;

const StyledTreeSelect = styled(TreeSelect)`
    width: 100%;
    display: flex;
`;
