import { dayConfig } from './day';
import { weekConfig } from './week';
import { monthConfig } from './month';
import { yearConfig } from './year';

export default Object.assign({
    day: dayConfig,
    week: weekConfig,
    month: monthConfig,
    year: yearConfig,
});

