import React, { useState, useContext } from 'react';
import { Button, Checkbox, Tabs } from 'antd';

import TextField from '../../../../components/CRUDs/TextField';
import NumberField from '../../../../components/CRUDs/NumberField';
import SelectField from '../../../../components/CRUDs/SelectField';

import { AppContext } from '../../../../AppDataProvider';
import { AdministrationContext } from '../../../Administration/AdministrationDataProvider';

const { TabPane } = Tabs;

const BuildingForm = ({
  editable,
  initialState,
  actionLabel,
  onCancel,
  onAction,
  removeMode = false,
}) => {
  const data = initialState || {
    // Admin
    fullName: '',
    shortName: '',
    street: '',
    city: '',
    postalCode: '',
    postOffice: '',
    hasMetersInstalled: true,
    category: 1,
    userId: 0,
    // User
    yearOfConstruction: 0,
    modernisationDescription: '',
    hasThermostaticValves: true,
    area: 0,
    cubature: 0,
    mainElectricityHookUpPower: 0,
    mainHeatHookUpPower: 0,
    coolingType: 1,
    lightingType: 1,
    ventilationType: 1,
    heatingSourceType: 1,
    heatingEnergyCarrierType: 1,
    renewableEnergySourceType: 1,
    domesticHotWaterSourceType: 1,
  };

  const { enums } = useContext(AppContext);
  const { users } = useContext(AdministrationContext);

  const [formData, setFormData] = useState(data);

  const onChange = (fieldName, e) => {
    setFormData({
      ...formData,
      [fieldName]: e.target.value,
    });
  };

  const handleAction = () => {
    onAction(formData);
    onCancel();
  };

  const adminProps = { editable: false, formData };
  const adminTab = (
    <>
      <TextField {...adminProps} name="fullName" label="Pena Nazwa" />
      <TextField {...adminProps} name="shortName" label="Skrt" />
      <TextField {...adminProps} name="street" label="Ulica" />
      <TextField {...adminProps} name="city" label="Miejscowo" />
      <TextField {...adminProps} name="postalCode" label="Kod pocztowy" />
      <TextField {...adminProps} name="postOffice" label="Poczta" />
      <Checkbox checked={formData.hasMetersInstalled} disabled={false}>
        Budynek opomiarowany
      </Checkbox>
      <SelectField
        disabled
        value={formData.category}
        items={enums.buildingTypes}
      />
      <SelectField disabled value={formData.userId} items={users} />
    </>
  );

  const handleValvesCheck = e =>
    setFormData({
      ...formData,
      hasThermostaticValves: e.target.checked,
    });

  const onSelect = (fieldName, value) => {
    setFormData({
      ...formData,
      [fieldName]: value,
    });
  };

  const userProps = { editable, onChange: onSelect, formData };

  const userTab = (
    <div style={{ height: 500, overflowY: 'scroll' }}>
      <NumberField
        {...userProps}
        name="yearOfConstruction"
        label="Rok budowy"
      />
      <TextField
        {...userProps}
        onChange={onChange}
        name="modernisationDescription"
        label="Opis modernizacji"
      />
      <Checkbox
        checked={formData.hasThermostaticValves}
        disabled={!editable}
        onChange={handleValvesCheck}
      >
        Posiada zawory termostatyczne
      </Checkbox>
      <NumberField {...userProps} name="area" label="Powierzchnia" />
      <NumberField {...userProps} name="cubature" label="Kubatura" />
      <NumberField
        {...userProps}
        name="mainElectricityHookUpPower"
        label="Moc gwnego przycza elektrycznego"
      />
      <NumberField
        {...userProps}
        name="mainHeatHookUpPower"
        label="Moc gwnego przycza cieplnego"
      />
      <SelectField
        placeholder="Wybierz typ chodzenia"
        onChange={v => onSelect('coolingType', v)}
        disabled={!editable}
        value={formData.coolingType}
        items={enums.coolingTypes}
      />
      <SelectField
        placeholder="Wybierz typ owietlenia"
        onChange={v => onSelect('lightingType', v)}
        disabled={!editable}
        value={formData.lightingType}
        items={enums.lightingTypes}
      />
      <SelectField
        placeholder="Wybierz typ wentylacji"
        onChange={v => onSelect('ventilationType', v)}
        disabled={!editable}
        value={formData.ventilationType}
        items={enums.ventilationTypes}
      />
      <SelectField
        placeholder="Wybierz typ rda ciepa"
        onChange={v => onSelect('heatingSourceType', v)}
        disabled={!editable}
        value={formData.heatingSourceType}
        items={enums.heatingSourceTypes}
      />
      <SelectField
        placeholder="Wybierz typ nonika energii cieplnej"
        onChange={v => onSelect('heatingEnergyCarrierType', v)}
        disabled={!editable}
        value={formData.heatingEnergyCarrierType}
        items={enums.heatingEnergyCarrierTypes}
      />
      <SelectField
        placeholder="Wybierz typ odnawialnego rda energii"
        onChange={v => onSelect('renewableEnergySourceType', v)}
        disabled={!editable}
        value={formData.renewableEnergySourceType}
        items={enums.renewableEnergySourceTypes}
      />
      <SelectField
        placeholder="Wybierz typ rda ciepej wody"
        onChange={v => onSelect('domesticHotWaterSourceType', v)}
        disabled={!editable}
        value={formData.domesticHotWaterSourceType}
        items={enums.domesticHotWaterSourceTypes}
      />
    </div>
  );

  return (
    <div>
      {removeMode ? (
        <div>Czy na pewno chcesz usun budynek ? </div>
      ) : (
        <Tabs defaultActiveKey="userData">
          <TabPane tab="Dane admnistratora" key="adminData">
            {adminTab}
          </TabPane>
          <TabPane tab="Dane uytkownika" key="userData">
            {userTab}
          </TabPane>
        </Tabs>
      )}
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          marginTop: '20px',
          padding: '0 30px',
        }}
      >
        <Button onClick={onCancel}>Anuluj</Button>
        <Button type="primary" onClick={handleAction}>
          {actionLabel}
        </Button>
      </div>
    </div>
  );
};

export default BuildingForm;
