import React from 'react';
import styled from 'styled-components';

import { formatNumber } from '../../../utils/formaters';
import AreaChart from '../../../components/Charts/AreaChart';

const exampleConfig = {
    chartConfig: {
      settings: {
        stopAnimation: true,
        stopInteractive: true,
        title: "",
        showTitle: false,
        showLegend: false,
        horizontalGrid: true,
        verticalGrid: false,
        stackData: true,
        yFormatter: (e) => formatNumber(e, {position: -1}),
      },
      xAxis: {
        xKey: "time",
        description: "Czas",
        showDescription: false,
        color: "#333",
        datesNumber: 4,
        withTime: false,
      },
      yAxises: [
        {
          id: "0",
          color: "#333",
          showAxis: true,
          side: "left",
          description: "Zuycie ciepa",
          showDescription: true,
          unit: "%",
          rangeFrom: "0",
          rangeTo: "100",
          rangeSpan: "auto",
        },
      ],
    },
    usedData: [
      {
        id: "0",
        description: "Dzienne zuycie ciepa",
        dataSource: "dailyHeatConsumption",
        axis: "0",
        lineWidth: 2,
        lineColor: "#063951",
        unit: "%",
      },
      {
        id: "1",
        description: "Dzienne zuycie prdu",
        dataSource: "dailyElectricityConsumption",
        axis: "0",
        dotted: false,
        lineWidth: 2,
        lineColor: "#C13015",
        unit: "%",
      },
      {
        id: "2",
        description: "Dzienne zuycie wody",
        dataSource: "dailyWaterConsumption",
        axis: "0",
        lineWidth: 2,
        lineColor: "#0D95BC",
        unit: "%",
      },
    ],
};

const begin = new Date("2019-05-03").getTime();

const exampleData = () => Array(50).fill(0).map((e, i) => ({
    time: begin + i * 1000 * 60 * 60 * 24,
    dailyHeatConsumption: 20 * Math.random() + 30,
    dailyElectricityConsumption: 10 * Math.random() + 15,
})).map(e => ({...e, dailyWaterConsumption: 100 - e.dailyElectricityConsumption - e.dailyHeatConsumption}));

export default () => (
    <StyledExample>
        <AreaChart data={exampleData()} {...exampleConfig} />
    </StyledExample>
);

const StyledExample = styled.div`
    height: 540px;
    width: 900px;
`;
