import React, { useContext, useState } from 'react';
import { Redirect } from 'react-router';
import styled from 'styled-components';
import { Input, Button } from 'antd';

import { AppContext } from '../../AppDataProvider';
import { DashboardRoute } from '../../Routes';

const SignIn = () => {
  const { isSignedIn, isLoading, submitLogin } = useContext(AppContext);
  const [email, setFormEmail] = useState('');
  const [password, setFormPassword] = useState('');

  return isSignedIn ? (
    <Redirect to={DashboardRoute} />
  ) : (
    <Form>
      <Title>KAPE</Title>
      <Subtitle>monitor zuycia energii</Subtitle>
      <StyledInput
        placeholder="Email"
        value={email}
        onChange={e => setFormEmail(e.target.value)}
      />
      <StyledInput
        placeholder="Haso"
        type="password"
        value={password}
        onChange={e => setFormPassword(e.target.value)}
      />
      <StyledButton
        disabled={!isFormValid() || isLoading}
        onClick={() => submitLogin(email, password)}
      >
        Zaloguj
      </StyledButton>
    </Form>
  );

  function isFormValid() {
    return email.length > 0 && password.length > 0;
  }
};

const Form = styled.form`
  width: 600px;
  display: flex;
  position: absolute;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
  flex-direction: column;
  align-items: center;
  padding: 50px;
  box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
`;

const Title = styled.h1`
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 0px;
`;

const Subtitle = styled.h2`
  font-size: 24px;
  font-weight: bold;
  color: #1890ff;
  margin-bottom: 20px;
`;

const StyledInput = styled(Input)`
  margin: 10px !important;
`;

const StyledButton = styled(Button)`
  margin-top: 15px;
`;

export default SignIn;
