import React from 'react';
import styled from 'styled-components';

import Page from './Page';
import PageHeading from './PageHeading';

export default ({community, buildings}) => (
    <Page>
        <PageHeading id="Navigation" to="Navigation">
            Spis treci
        </PageHeading>
        <NavigationSection>
            <CustomLink href="#Community">{`Gmina ${community.name}`}</CustomLink>
        </NavigationSection>
        <NavigationSection>
            <CustomLink href="#Buildings">Budynki</CustomLink>
            <ReportLinks>
                {buildings.map(({id, name}) => (
                    <ReportLink
                        to={genReportLink(id)}
                        key={id}
                    >
                        {name}
                    </ReportLink>
                ))}
            </ReportLinks>
        </NavigationSection>
        <NavigationSection>
            <CustomLink href="#Summary">Podsumowanie</CustomLink>
        </NavigationSection>
    </Page>);

export const genReportLink = (id) => `Building_${id}`;

const ReportLinks = styled.ol`
    list-style-type: decimal;
    padding: 0 32px;
`;

const CustomLink = styled.a`
    text-decoration: none;
    color: darkblue;
`;

const NavigationSection = styled.div`
    & > ${CustomLink} {
        font-size: 20px;
    }
    ${CustomLink} + ${ReportLinks} {
        margin-top: 8px;
    }
    & + & {
        margin-top: 8px;
    }
`;

const StyledReportLink = styled.li`
    & + & {
        margin-top: 8px;
    }
    
`;

const ReportLink = ({to, children, ...props}) =>
    (<StyledReportLink {...props}>
        <CustomLink href={`#${to}`}>{children}</CustomLink>
    </StyledReportLink>);
