import React from 'react';
import ReactDOM from 'react-dom';
import to from 'await-to-js';
import { BrowserRouter as Router } from 'react-router-dom';
import axios from 'axios';
import { createGlobalStyle } from 'styled-components';
import reset from 'styled-reset';

import 'antd/dist/antd.css';

import App from './App';
import { getEnums } from './scenes/SignIn/services/api';
import { getBuildings } from './scenes/Administration/services/api';

axios.defaults.baseURL = process.env.REACT_APP_API_BASE;

// console.log(process.env.REACT_APP_API_BASE, 'base');
const token = localStorage.getItem('user-token');
if (token) {
  axios.defaults.headers.common.Authorization = `Bearer ${token}`;
}

const GlobalStyle = createGlobalStyle`
  ${reset}
`;

const getBaseData = async () => {
  const [err, buildings] = await to(getBuildings());
  if (err) console.err('Get buildings eroor: ', err);
  const [enumErr, enums] = await to(getEnums());
  if (enumErr) console.err('Get enums eroor: ', enumErr);

  // FIX ME MATEUSZ MICHALEWSKI :)
  buildings.data = [
    {
      id: 1,
      name: 'SP nr 17',
      measurements: [
        'temperature',
        'co2',
        'humidity',
        'electricity',
        'hotWater',
      ],
    },
    {
      id: 2,
      name: 'Muzeum Kultury',
      measurements: ['temperature', 'co2', 'humidity'],
    },
    {
      id: 44,
      name: 'Dom seniora',
      measurements: ['electricity', 'hotWater'],
    },
  ];

  return {
    enums: enums.data,
    buildings: buildings.data,
    appHeader: 'KAPE',
  };
};

if (localStorage.getItem('user-token')) {
  getBaseData().then(data => {
    renderApp(data);
  });
} else {
  renderApp();
}

function renderApp(data) {
  const app = (
    <React.Fragment>
      <GlobalStyle />
      <Router>
        <App appBaseData={data} />
      </Router>
    </React.Fragment>
  );

  ReactDOM.render(app, document.getElementById('root'));
}
