import React, { useState, useContext } from 'react';

import { AppContext } from '../../AppDataProvider';
import {
  getBuildings,
  postBuildingUpdate,
} from '../Administration/services/api';
import {
  getHookUps,
  getInvoices,
  postHookUpRemoval,
  postHookUpUpdate,
  postInvoiceRemoval,
  postInvoiceUpdate,
  postNewHookUp,
  postNewInvoice,
} from './services/api';

const DbContext = React.createContext({});

const DbDataProvider = ({ children }) => {
  const { startAsyncTask, finishAsyncTask } = useContext(AppContext);

  const [buildings, setBuildings] = useState([]);
  const [hookUps, setHookUps] = useState([]);
  const [invoices, setInvoices] = useState([]);

  const getData = () => {
    let data;
    if (kind === 'Buildings') {
      data = buildings;
    } else if (kind === 'HookUps') {
      data = hookUps;
    } else {
      data = invoices;
    }
    return data;
  };
  const getCurrentEntry = () => {
    const data = getData();
    const currentEntry = data.find(entry => entry.id === currentId);
    return currentEntry;
  };
  const getIndexById = id => {
    const data = getData();
    const index = data.findIndex(entry => entry.id === id);
    return index;
  };

  async function fetchBuildings() {
    startAsyncTask();
    const { data } = await getBuildings();
    finishAsyncTask();
    setBuildings(data);
  }

  async function fetchHookUps() {
    startAsyncTask();
    const { data } = await getHookUps();
    finishAsyncTask();
    setHookUps(data);
  }

  async function fetchInvoices() {
    startAsyncTask();
    const { data } = await getInvoices();
    finishAsyncTask();
    setInvoices(data);
  }

  // Buildings

  function updateBuilding(updatedBuilding) {
    postBuildingUpdate(updatedBuilding);
    const newBuildings = [...buildings];
    const updateIndex = getIndexById(updatedBuilding.id);
    newBuildings[updateIndex] = updatedBuilding;
    setBuildings(newBuildings);
  }

  // HookUps

  function createHookUp(newHookUps) {
    postNewHookUp({ ...newHookUps });
    setHookUps([...hookUps, newHookUps]);
  }
  function updateHookUp(updatedHookUps) {
    postHookUpUpdate(updatedHookUps);
    const newHookUps = [...hookUps];
    const updateIndex = getIndexById(updatedHookUps.id);
    newHookUps[updateIndex] = updatedHookUps;
    setHookUps(newHookUps);
  }
  function deleteHookUp(userToDelete) {
    postHookUpRemoval(userToDelete.id);
    const newHookUpss = [...hookUps];
    const deleteIndex = getIndexById(userToDelete.id);
    newHookUpss.splice(deleteIndex, 1);
    setHookUps(newHookUpss);
  }

  function createInvoice(newInvoices) {
    postNewInvoice({ ...newInvoices });
    setInvoices([...invoices, newInvoices]);
  }
  function updateInvoice(updatedInvoices) {
    postInvoiceUpdate(updatedInvoices);
    const newInvoicess = [...invoices];
    const updateIndex = getIndexById(updatedInvoices.id);
    newInvoicess[updateIndex] = updatedInvoices;
    setInvoices(newInvoicess);
  }
  function deleteInvoice(userToDelete) {
    postInvoiceRemoval(userToDelete.id);
    const newInvoices = [...invoices];
    const deleteIndex = getIndexById(userToDelete.id);
    newInvoices.splice(deleteIndex, 1);
    setInvoices(newInvoices);
  }

  //  createHookUp,
  //  updateHookUp,
  //  deleteHookUp,
  //  // Invoices
  //  createInvoice,
  //  updateInvoice,
  //  deleteInvoice,

  // COMMMON

  const [currentId, setCurrentId] = useState(undefined);
  const [mode, setMode] = useState(undefined);
  const [kind, setKind] = useState(undefined);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const state = {
    buildings,
    hookUps,
    invoices,

    fetchBuildings,
    fetchHookUps,
    fetchInvoices,

    // Buildings
    updateBuilding,
    // HookUps
    createHookUp,
    updateHookUp,
    deleteHookUp,
    // Invoices
    createInvoice,
    updateInvoice,
    deleteInvoice,
    mode,
    setMode,
    kind,
    setKind,
    currentConfig: `${mode}${kind}`,

    setCurrentId,
    currentEntry: getCurrentEntry(),

    isModalVisible,
    setIsModalVisible,
  };

  return <DbContext.Provider value={state}>{children}</DbContext.Provider>;
};

export { DbContext };
export default DbDataProvider;
