import React from 'react';
import styled from 'styled-components';
import Page from './Page';

export default ({name, author, date, ...props}) => (
    <StyledFrontPage {...props}>
        <Title>{`Raport dla gminy ${name}`}</Title>
        <Logo>
            <img src="https://www.kape.gov.pl/web/image/51993" alt="Nie zaadowano logo" />
        </Logo>
    </StyledFrontPage>
);

const StyledFrontPage = styled(Page)`
    display: none;
    @media print {
        display: unset;
    }
`;

const Title = styled.div`
    font-weight: bold;
    font-size: 64px;
    text-align: center;
`;

const Logo = styled.div`
    text-align: center;
    margin-top: 64px;
    img {
        width: 640px;
    }
`;
