import sleep from '../../../sleep';

// importy z mockw
const building = require('./mocks/buildingData.json');
const current = require('./mocks/current.json');
const day = require('./mocks/day.json');
const week = require('./mocks/week.json');
const month = require('./mocks/month.json');
const year = require('./mocks/year.json');

export const getBuildingData = async (id) => {
  await sleep(3000);
  return {
    status: 200,
    data: building,
  };
};

export const getBuildingActual = async (id) => {
  await sleep(2000);
  return {
    status: 200,
    data: current,
  };
};

export const getBuildingTime = async (id, time) => {
  await sleep( time === 'day' ? 500 : 1500);
  const data = ({
    day,
    week,
    month,
    year,
  })[time];
  return {
    status: 200,
    data,
  };
};
