import React, { useContext, useEffect, useState } from 'react';
import styled from 'styled-components';
import { filter } from 'lodash';
import { Transfer, Empty } from 'antd';

import { AppContext } from '../../../AppDataProvider';

import MathParser from '../../../components/MathParser';
import MultiCheckboxGroup from '../../../components/MultiCheckboxGroup';

import { removePolish } from '../../../utils/formaters';

const measurementsConfig = require('./measurementsConfig.json');

export const getMeasurementLabel = (key) =>
    measurementsConfig.measurements.find(e => e.key === key).label;

export const getMeasurementUnit = (key) =>
    measurementsConfig.measurements.find(e => e.key === key).unit;

const filterOption = (inputValue, option) =>
    removePolish(option.label.toLowerCase()).includes(removePolish(inputValue.toLowerCase()));

export const MeasurementsPickerTransfer = ({onChange, building, value, disabledValues = [], ...props}) => {
    const { buildings } = useContext(AppContext);
    const measurements = building &&
        buildings.find(({id}) => id === building).measurements;
    const dataSource = measurements && filter(measurementsConfig.measurements,
        ({key}) => measurements.includes(key));

    const [selectedKeys, setSelectedKeys] = useState();

    useEffect(() => {
        setSelectedKeys([]);
    }, [building]);

    const handleSelectChange = (sourceSelectedKeys, targetSelectedKeys) => {
        setSelectedKeys([...sourceSelectedKeys, ...targetSelectedKeys]);
    };
    
    return measurements ?
    (<MeasurementsPicker>
        <Header>
            <HeaderItem>Potencjalne dane</HeaderItem>
            <HeaderItem>Wybrane dane</HeaderItem>
        </Header>
        <StyledTransfer
            dataSource={dataSource.map((e) => ({
                ...e,
                disabled: disabledValues.includes(e.key),
            }))}
            showSearch
            filterOption={filterOption}
            onChange={onChange}
            onSelectChange={handleSelectChange}
            render={item => <MathParser>{item.label}</MathParser>}
            {...props}
            targetKeys={value}
            selectedKeys={selectedKeys}
        />
    </MeasurementsPicker>)
    :
    (<Empty
        description={
            <span>
                Wybierz budynek by zobaczy oprzyrzdowane dane
            </span>
        }
    />);
}

export default ({onChange, building, value, disabledValues = [], ...props}) => {
    const { buildings } = useContext(AppContext);
    const measurements = building &&
        buildings.find(({id}) => id === building).measurements;
    const dataSource = measurements && filter(measurementsConfig.measurements,
        ({key}) => measurements.includes(key));
    
    return measurements ?
    (<MultiCheckboxGroup
        options={dataSource.map((e) => ({
            ...e,
            disabled: disabledValues.includes(e.key),
        }))}
        onChange={onChange}
        allowAll
        allowSearch
        filterOption={filterOption}
        renderItem={item => <MathParser>{item.label}</MathParser>}
        value={value}
        {...props}
    />)
    :
    (<Empty
        description={
            <span>
                Wybierz budynek by zobaczy oprzyrzdowane dane
            </span>
        }
    />);
}

const MeasurementsPicker = styled.div``;

const HeaderItem = styled.div`
    flex: 1;
    text-align: center;
`;

const Header = styled.div`
    display: flex;
    & ${HeaderItem}:first-child {
        margin-right: 24px;
    }
    & ${HeaderItem}:last-child {
        margin-left: 24px;
    }
    margin-bottom: 8px;
    font-size: 0.9rem;
`;

const StyledTransfer = styled(Transfer)`
    display: flex;
    align-items: center;
    & .ant-transfer-list {
        width: unset;
        flex: 1 0 180px;
    }
`;
