import React, { useContext } from 'react';
import styled from 'styled-components';
import { Button } from 'antd';

import AlarmsWarning from './AlarmWarning';
import { AppContext } from '../AppDataProvider';

const AppBar = () => {
  const { setIsSignedIn, appHeader, anyActiveAlarms } = useContext(AppContext);

  return (
    <Bar>
      <LeftGroup>
        <Title>KAPE</Title>
      </LeftGroup>
      <CenterGroup>
        <MonitorTitle>{appHeader}</MonitorTitle>
      </CenterGroup>
      <RightGroup>
        {anyActiveAlarms && <AlarmsWarning />}
        <Button onClick={logOut}>wyloguj</Button>
      </RightGroup>
    </Bar>
  );

  function logOut() {
    setIsSignedIn(false);
    localStorage.removeItem('user-token');
  }
};

const Bar = styled.div`
  display: flex;
  background: #1890ff;
  justify-content: space-between;
  padding: 10px 0;
  height: 56px;
`;

const Title = styled.h1`
  color: white;
  font-weight: bold;
  font-size: 30px;
  margin: 0 80px 0 15px;
`;

const LeftGroup = styled.div`
  margin-left: 50px;
  display: flex;
  align-items: center;
`;

const MonitorTitle = styled.div`
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
`;

const CenterGroup = styled.div`
  margin: 0 25px;
  display: flex;
  align-items: center;
`;

const RightGroup = styled.div`
  margin-right: 50px;
  display: flex;
  align-items: center;
`;

export default AppBar;
